import { NgModule, Optional, Provider } from '@angular/core';
import { CommonModule } from '@angular/common';

import { FeatureModule } from '@generic-ui/hermes';

import { SortingApiModule } from '../core/api/sorting.api-module';


@NgModule({
	imports: [
		CommonModule,

		SortingApiModule
	],
	declarations: [],
	exports: []
})
export class SortingFeatureModule extends FeatureModule {

	static forComponent(): Array<Provider> {
		return [];
	}

	constructor(@Optional() sortingApiModule: SortingApiModule) {
		super();
		if (sortingApiModule === null) {
			throw new Error('SortingApiModule is required.');
		}
	}

}
