import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	EventEmitter,
	Output,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';

import { SmartComponent } from '../../../../../common/cdk/component/smart-component';


@Component({
	selector: 'div[gui-filter-value]',
	templateUrl: './filter-value.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FilterValueComponent extends SmartComponent {

	private static readonly FORM_FILTER_VALUE = 'filterValue';

	@ViewChild('formRef', { static: false })
	formRef: ElementRef;

	@Output()
	valueChanged = new EventEmitter();

	filterForm: FormGroup;

	constructor(detector: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly formBuilder: FormBuilder) {
		super(detector, elementRef);

		let controlsConfig = {};
		controlsConfig[FilterValueComponent.FORM_FILTER_VALUE] = '';

		this.filterForm = this.formBuilder.group(controlsConfig);
	}

	ngOnInit() {
		this.filterForm
			.controls[FilterValueComponent.FORM_FILTER_VALUE]
			.valueChanges
			// .pipe(
			// 	this.takeUntil()
			// )
			.subscribe((value: string) => {
				this.valueChanged.emit(value);
			});
	}

	protected getSelectorName(): string {
		return 'gui-filter-value';
	}

}
