import { GuiComponent } from './gui-component';
import { ElementRef } from '@angular/core';

export abstract class PureComponent extends GuiComponent {

	private readonly subClassConstructor: Function;

	private readonly subClassNgOnInit: Function;

	protected constructor(elementRef: ElementRef) {
		super(elementRef);
		this.subClassConstructor = this.constructor;
		this.subClassNgOnInit = (this as any).ngOnInit;

		if (!this.hasConstructorOnlyElementRefInjected(arguments)) {
			this.throwError('it should not inject services');
		}

		if (this.subClassNgOnInit) {
			this.throwError('it should not use ngOnInit');
		}
	}

	private hasConstructorOnlyElementRefInjected(args: IArguments): boolean {

		if (arguments.length > 1) {
			return false;
		}

		if (arguments.length === 1) {
			return this.isElementRef(arguments[0]);
		}

		return false;
	}

	private isElementRef(elRef: any): boolean {
		return elRef.nativeElement !== null;
	}

	private throwError(reason: string): void {
		throw new Error(`Component "${this.subClassConstructor.name}" is a PureComponent, ${reason}.`);
	}
}
