import { NgModule, Provider } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';

import { FabricModule } from '@generic-ui/fabric';
import { FeatureModule } from '@generic-ui/hermes';

import { SearchApiModule } from '../core/api/search.api-module';
import { SearchIconComponent } from './icon/search-icon.component';
import { SearchComponent } from './search.component';


@NgModule({
	imports: [
		CommonModule,
		FabricModule,
		SearchApiModule,
		ReactiveFormsModule
	],
	declarations: [
		SearchIconComponent,
		SearchComponent
	],
	exports: [
		SearchIconComponent,
		SearchComponent
	]
})
export class SearchFeatureModule extends FeatureModule {

	static forComponent(): Array<Provider> {
		return [];
	}

	constructor(searchApiModule: SearchApiModule) {
		super();
		if (searchApiModule === null) {
			throw new Error('SearchApiModule is required');
		}
	}

}
