import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { FilterTypeReadModel } from '../../../core/api/type/filter-type.read-model';
import { FilterTypeId } from '../../../core/domain/type/filter-type.id';
import { PureComponent } from '../../../../../common/cdk/component/pure-component';

@Component({
	selector: 'div[gui-filter-type-selector][filterTypes]',
	templateUrl: `./filter-type-selector.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FilterTypeSelectorComponent extends PureComponent {

	@Input()
	filterTypes: Array<FilterTypeReadModel>;

	@Output()
	filterTypeSelected = new EventEmitter<FilterTypeId>();

	constructor(elementRef: ElementRef) {
		super(elementRef);
	}

	onSelectChange(filterType: FilterTypeReadModel): void {
		this.filterTypeSelected.emit(filterType.getId());
	}

	protected getSelectorName(): string {
		return 'gui-filter-type-selector';
	}

}
