import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Input,
	OnChanges,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';

import { CellTemplateWithAccessor } from '../../../../../../composition/core/domain-read/definition/cell-template-with-accessor';
import { ChangedValueEmitter } from '../../../../../../composition/core/domain-read/edit/changed-value.emitter';
import { EditEventType } from '../../../../../../composition/core/domain-read/edit/state/edit.event-type';
import { ItemEntity } from '../../../../../source/core/domain/core/item/item.entity';
import { SmartComponent } from '../../../../../../common/cdk/component/smart-component';
import { StructureEditSourceItemParams } from '../../../../../source/core/domain/origin/edit/structure.edit-source-item.params';
import { SourceCommandInvoker } from '../../../../../source/core/api/source.command-invoker';
import { StructureCellEditState } from '../../../edit/structure.cell-edit.state';
import { NgChanges } from '../../../../../../common/cdk/component/ng-changes';

@Component({
	selector: 'div[gui-structure-cell-edit][entity][cell]',
	template: `

		<span #cellContainer>
			<ng-container
					*ngTemplateOutlet="cell.editTemplate;
									context: editContext">
			</ng-container>
		</span>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureCellEditComponent extends SmartComponent implements OnChanges {

	@ViewChild('cellContainer', { static: true })
	cellContainerRef: ElementRef;

	@Input()
	entity: ItemEntity;

	@Input()
	cell: CellTemplateWithAccessor;

	editContext: any;

	valueChanges$: ChangedValueEmitter<any>;

	status$: ChangedValueEmitter<any>;

	actualValue: any;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly sourceCommandService: SourceCommandInvoker) {
		super(changeDetectorRef, elementRef);
	}

	ngOnChanges(changes: NgChanges<StructureCellEditComponent>) {

		if (changes.entity !== null && changes.entity !== undefined) {
			this.initEditContext();
		}

		if (changes.cell !== null && changes.cell !== undefined) {
			this.initEditContext();
		}
	}

	ngOnInit() {
		this.initEditContext();
	}

	submitChangesAndExit(): void {

		const itemId = this.entity.getId(),
			updatedValue = this.actualValue,
			fieldId = this.cell.columnFieldId;

		this.sourceCommandService.editItem(new StructureEditSourceItemParams(itemId, fieldId, updatedValue));
	}

	private initEditContext(): void {
		this.valueChanges$ = new ChangedValueEmitter();
		this.status$ = new ChangedValueEmitter<EditEventType>();

		this.editContext = {
			status: this.status$,
			valueChanges: this.valueChanges$,
			value: this.cell.getValue(this.entity),
			focus: false,
			parent: this.cellContainerRef
		};

		this.status$
			.on()
			.pipe(
				this.takeUntil()
			)
			.subscribe((status: EditEventType) => {

				switch (status) {
					case EditEventType.SUBMIT:
						this.submitChangesAndExit();
						this.publishEditSubmit();
						break;

					case EditEventType.CANCEL:
						// this.exitEditMode();
						this.publishEditCancel();
						break;

					default:
						break;
				}
			});
	}

	private observeValueChanges(): void {

		this.valueChanges$
			.on()
			.pipe(
				this.takeUntil()
			)
			.subscribe((value: any) => {
				this.actualValue = value;
			});
	}

	private publishEditState(state: StructureCellEditState): void {
		// this.structureCellEditStore.next(state);
	}

	private publishEditEnter(): void {
		this.publishEditState(StructureCellEditState.ENTER);
	}

	private publishEditCancel(): void {
		this.publishEditState(StructureCellEditState.CANCEL);
	}

	private publishEditSubmit(): void {
		this.publishEditState(StructureCellEditState.SUBMIT);
	}

	protected getSelectorName(): string {
		return 'gui-structure-cell-edit';
	}

}
