import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { combineLatest } from 'rxjs';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';

import { CellTemplateWithAccessor } from '../../../../composition/core/domain-read/definition/cell-template-with-accessor';
import { FormationCommandInvoker } from '../../../source/core/api/formation/formation.command-invoker';
import { StructureCellEditArchive } from '../edit/structure.cell-edit.archive';
import { StructureEditModeArchive } from '../edit/structure.edit-mode.archive';
import { FormationWarehouse } from '../../../source/core/api/formation/formation.warehouse';
import { ItemEntity } from '../../../source/core/domain/core/item/item.entity';
import { VerticalFormationRepository } from '../../../vertical-formation/core/api/vertical-formation.repository';
import { StructureWarehouse } from '../../core/api/structure.warehouse';
import { StructureId } from '../../../core/api/structure.id';
import { SearchPhraseRepository } from '../../../search/core/api/phrase/search-phrase.repository';
import { SearchHighlightArchive } from '../../../search/core/api/highlight/search-highlight.archive';
import { StructureVerticalFormationWarehouse } from '../../../vertical-formation/core/api/structure-vertical-formation.warehouse';
import { RowSelectionTypeArchive } from '../../../source/core/api/formation/type/row-selection-type.archive';
import { RowSelectionType } from '../../../source/core/api/row-selection';
import { RowSelectToggleType } from '../../../source/core/domain/formation/core/row-select-toggle-type';
import { SchemaRowClassArchive } from '../../../../schema/core/api/styling/schema.row-class.archive';
import { SchemaRowStyleArchive } from '../../../../schema/core/api/styling/schema.row-style.archive';
import { SchemaRowClass } from '../../../../schema/core/api/styling/schema.row-class';
import { SchemaRowStyle } from '../../../../schema/core/api/styling/schema.row-style';


@Component({
	selector: 'div[gui-structure-content]',
	templateUrl: `./structure-content.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureContentComponent extends SmartComponent implements OnInit {

	@Input()
	source: Array<ItemEntity>;

	@Input()
	columns: Array<CellTemplateWithAccessor>;

	editMode = false;

	cellEditing = false;

	rowHeight: number;

	searchPhrase: string = '';

	highlighting: boolean = true;

	phrase: boolean = true;

	rowDetailOpened = 4;

	checkboxSelection: boolean = false;

	schemaRowClass: SchemaRowClass;

	schemaRowStyle: SchemaRowStyle;

	constructor(private readonly elementRef: ElementRef,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly formationCommandService: FormationCommandInvoker,
				private readonly structureCellEditArchive: StructureCellEditArchive,
				private readonly structureEditModeArchive: StructureEditModeArchive,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly structureVerticalFormationWarehouse: StructureVerticalFormationWarehouse,
				private readonly verticalFormationRepository: VerticalFormationRepository,
				private readonly structureId: StructureId, // REfactor
				private readonly rowSelectionTypeArchive: RowSelectionTypeArchive,
				private readonly structureSearchPhraseRepository: SearchPhraseRepository,
				private readonly structureSearchHighlightArchive: SearchHighlightArchive,
				private readonly schemaRowClassArchive: SchemaRowClassArchive,
				private readonly schemaRowStyleArchive: SchemaRowStyleArchive) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.rowSelectionTypeArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((type: RowSelectionType) => {
				this.checkboxSelection = type === RowSelectionType.CHECKBOX;
				this.reRender();
			});

		this.structureEditModeArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.editMode = enabled;
				this.reRender();
			});

		this.structureCellEditArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.cellEditing = enabled;
				this.reRender();
			});

		this.structureVerticalFormationWarehouse
			.onRowHeight(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowHeight: number) => {
				this.rowHeight = rowHeight;
				this.reRender();
			});

		combineLatest(
			this.structureSearchPhraseRepository
				.onPhrase(this.structureId),
			this.structureSearchHighlightArchive
				.onValue()
		).pipe(
			this.takeUntil()
		 )
		 .subscribe((args: Array<any>) => {

			 const searchPhrase = args[0],
				 highlighting = args[1];

			 this.highlighting = highlighting;
			 this.searchPhrase = searchPhrase;

			 if (!this.highlighting) {
				 this.searchPhrase = '';
			 }

			 this.reRender();
		 });

		this.schemaRowClassArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((schemaRowClass: SchemaRowClass) => {
				this.schemaRowClass = schemaRowClass;
				this.reRender();
			});

		this.schemaRowStyleArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((schemaRowStyle: SchemaRowStyle) => {
				this.schemaRowStyle = schemaRowStyle;
				this.reRender();
			});
	}

	trackByFn() {
		return 0;
	}

	translateY(index: number): string {

		let height = index * this.rowHeight;

		return `translateY(${height}px)`;
	}

	toggleSelectedRow(entity: ItemEntity): void {
		if (!this.checkboxSelection) {
			this.formationCommandService.toggleSelectedRow(entity.getId(), RowSelectToggleType.NONE);
		}
	}

	protected getSelectorName(): string {
		return 'gui-structure-content';
	}

}
