import { AttributeModifierApi } from './attribute-modifier-api';
import { Modifier } from '../core/modifier';


export class AttributeModifier extends Modifier<AttributeModifierApi> {

	constructor(
		private readonly htmlElement?: HTMLElement
	) {
		super(htmlElement);
	}

	protected createModifier(htmlElement: HTMLElement): AttributeModifierApi {
		return new AttributeModifier.AttributeModifier(htmlElement);
	}

	private static readonly AttributeModifier = class implements AttributeModifierApi {

		constructor(
			private readonly htmlElement: HTMLElement
		) {
		}

		setAttribute(name: string, value: string): void {
			this.htmlElement.setAttribute(name, value);
		}

		removeAttribute(name: string): void {
			this.htmlElement.removeAttribute(name);
		}

	};

}
