import { StructureId } from '../../../core/api/structure.id';
import { SummariesValues } from './calculation/summaries.values';
import { StructureDomainEvent } from '../../../core/domain/structure.domain-event';


export class StructureSummariesChangedEvent extends StructureDomainEvent {

	constructor(structureId: StructureId,
				private readonly values: Map<string, SummariesValues>) {
		super(structureId, 'StructureSummariesChangedEvent');
	}

	getSummaries() {
		return this.values;
	}

}
