import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Inject,
	NgZone,
	OnDestroy,
	OnInit,
	Optional,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { combineLatest, fromEvent, Subject } from 'rxjs';
import { distinctUntilChanged, filter, map, takeUntil } from 'rxjs/operators';

import { ResizeDetector } from '@generic-ui/fabric';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { CellTemplateWithAccessor } from '../../../../composition/core/domain-read/definition/cell-template-with-accessor';
import { SchemaRowColoring } from '../../../../schema/core/api/schema-row-coloring';
import { StructureId } from '../../../core/api/structure.id';
import { StructureCommandDispatcher } from '../../../core/api/structure.command-dispatcher';
import { StructureWarehouse } from '../../core/api/structure.warehouse';
import { SourceWarehouse } from '../../../source/core/api/source.warehouse';
import { CompositionCommandInvoker } from '../../../../composition/core/api/composition.command-invoker';
import { CompositionWarehouse } from '../../../../composition/core/api/composition.warehouse';
import { FormationWarehouse } from '../../../source/core/api/formation/formation.warehouse';
import { structureParentComponent } from '../structure-parent-component';
import { StructureReadModelRoot } from '../../core/api/read/structure.read-model-root';
import { ItemEntity } from '../../../source/core/domain/core/item/item.entity';
import { StructureVerticalFormationWarehouse } from '../../../vertical-formation/core/api/structure-vertical-formation.warehouse';
import { StyleModifier } from '../../../../common/cdk/dom/style/style-modifier';


@Component({
	selector: 'div[gui-structure-container]',
	templateUrl: `./structure-container.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureContainerComponent extends SmartComponent implements OnInit, AfterViewInit, OnDestroy {

	@ViewChild('sourceCollection', { static: false })
	sourceCollectionRef: ElementRef;

	columns: Array<CellTemplateWithAccessor> = [];

	source: Array<ItemEntity> = [];

	height: number;

	rowColoring: SchemaRowColoring;

	private autoResizeWidthEnabled: boolean = false;

	private scrollObservation$ = new Subject();

	private readonly styleModifier: StyleModifier;

	constructor(changeDetectorRef: ChangeDetectorRef,
				private readonly elRef: ElementRef,
				private readonly ngZone: NgZone,
				private readonly structureId: StructureId,
				private readonly structureCommandService: StructureCommandDispatcher,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly structureVerticalFormationWarehouse: StructureVerticalFormationWarehouse,
				private readonly sourceWarehouse: SourceWarehouse,
				private readonly compositionCommandInvoker: CompositionCommandInvoker,
				private readonly compositionWarehouse: CompositionWarehouse,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly resizeDetector: ResizeDetector,
				@Optional() @Inject(structureParentComponent) private readonly structureParent: any) {
		super(changeDetectorRef, elRef);
		this.styleModifier = new StyleModifier(this.elRef.nativeElement);
	}

	ngOnInit() {

		this.structureVerticalFormationWarehouse
			.onContainerHeight(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((containerHeight: number) => {
				console.log(containerHeight)
				this.setContainerHeight(containerHeight);
			});

		combineLatest(
			this.sourceWarehouse.onEntities(),
			this.compositionWarehouse.onTemplateColumns()
		)
			.pipe(
				this.takeUntil()
			)
			.subscribe((arr: Array<any>) => {

				this.source = arr[0] as Array<ItemEntity>;
				this.columns = arr[1] as Array<CellTemplateWithAccessor>;

				this.reRender();
			});

		this.compositionWarehouse
			.onResizeWidth()
			.pipe(this.takeUntil())
			.subscribe((enabled: boolean) => {
				this.autoResizeWidthEnabled = enabled;
			});
	}

	ngAfterViewInit() {
		super.ngAfterViewInit();

		// turn on when structure is ready
		if (this.structureParent) {
			this.resizeDetector
				.observe(this.structureParent.getElementRef().nativeElement)
				.pipe(
					filter(() => this.autoResizeWidthEnabled),
					map((element: HTMLElement) => element.offsetWidth),
					distinctUntilChanged(),
					this.takeUntil()
				)
				.subscribe((width: number) => {
					this.recalculateContainer(width);
				});
		}

		this.compositionWarehouse
			.onContainerWidth()
			.pipe(
				this.takeUntil()
			)
			.subscribe((width: number) => {
				this.styleModifier.getElement(this.sourceCollectionRef.nativeElement).setWidth(width);
			});

		this.structureVerticalFormationWarehouse
			.onVerticalScrollEnabled(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				if (enabled) {
					this.enableScrollObservation();
				} else {
					this.disableScrollObservation();
				}
			});

		this.structureWarehouse
			.onStructure()
			.pipe(
				filter((str: StructureReadModelRoot) => {
					return str.isVerticalScrollEnabled();
				}),
				this.takeUntil()
			)
			.subscribe((structure: StructureReadModelRoot) => {

				const topMargin = structure.getTopMargin(),
					sourceHeight = structure.getSourceHeight();

				this.setSourceHeight(topMargin, sourceHeight);
			});

		this.structureVerticalFormationWarehouse
			.onScrollBarPosition()
			.pipe(
				this.takeUntil()
			)
			.subscribe((scrollPosition: number) => {

				this.elRef.nativeElement.scrollTop = scrollPosition;
			});
	}

	ngOnDestroy() {
		super.ngOnDestroy();
		this.resizeDetector.destroy(this.elRef.nativeElement);
	}

	private setContainerHeight(height: number): void {
		this.height = height;
		this.styleModifier.getHost().setHeight(height);
	}

	private setSourceHeight(topMargin: number, sourceHeight: number): void {
		this.styleModifier.getElement(this.sourceCollectionRef.nativeElement).setPaddingTop(topMargin);
		this.styleModifier.getElement(this.sourceCollectionRef.nativeElement).setHeight(sourceHeight);
	}

	private recalculateContainer(width: number): void {
		if (this.autoResizeWidthEnabled) {
			this.compositionCommandInvoker.setContainerWidth(width);
		}
	}

	private enableScrollObservation(): void {
		this.ngZone
			.runOutsideAngular(() => {

				fromEvent(this.elRef.nativeElement, 'scroll')
					.pipe(
						// auditTime(10),
						takeUntil(this.scrollObservation$),
						this.takeUntil()
					)
					.subscribe((event: any) => {

						const scrollTop = event.target.scrollTop;

						this.structureCommandService.setScrollPosition(scrollTop);
					});
			});
	}

	private disableScrollObservation(): void {
		this.scrollObservation$.next();
		this.scrollObservation$.complete();
	}

	protected getSelectorName(): string {
		return 'gui-structure-container';
	}

}
