import { StyleModifierApi } from './style-modifier-api';
import { StyleName } from './style-name';
import { Modifier } from '../core/modifier';


export class StyleModifier extends Modifier<StyleModifierApi> {

	constructor(
		private readonly htmlElement?: HTMLElement
	) {
		super(htmlElement);
	}

	protected createModifier(htmlElement: HTMLElement): StyleModifierApi {
		return new StyleModifier.StyleModifier(htmlElement);
	}

	private static readonly StyleModifier = class implements StyleModifierApi {

		private static readonly STYLE = 'style';

		constructor(
			private readonly htmlElement: HTMLElement
		) {
		}

		setStyleByName(name: string, value: string): void {
			this.set(name, value);
		}

		setWidth(value: number): void {
			this.set(StyleName.WIDTH, this.toPx(value));
		}

		setHeight(value: number): void {
			this.set(StyleName.HEIGHT, this.toPx(value));
		}

		setPaddingTop(value: number): void {
			this.set(StyleName.PADDING_TOP, this.toPx(value));
		}

		remove(styleName: StyleName): void {
			this.htmlElement[StyleModifier.StyleModifier.STYLE][styleName] = '';
		}

		removeStyleByName(name: string): void {
			this.htmlElement[StyleModifier.StyleModifier.STYLE][name] = '';
		}

		clear(): void {
			this.htmlElement.removeAttribute(StyleModifier.StyleModifier.STYLE);
		}

		private set(name: string, value: string): void {
			this.htmlElement[StyleModifier.StyleModifier.STYLE][name] = value;
		}

		private toPx(value: number): string {
			return value > 0 ? `${value}px` : `${value}`;
		}

	};

}
