import { Injectable } from '@angular/core';

import { CommandInvoker } from '@generic-ui/hermes';

import { SchemaTheme } from './schema-theme';
import { schemaGlobalId } from '../domain-read/schema.global-id';
import { SchemaDispatcher } from '../domain/schema.dispatcher';
import { SchemaReadModelRootId } from '../domain-read/schema.read-model-root-id';
import { SchemaRowColoring } from './schema-row-coloring';
import { StructureId } from '../../../structure/core/api/structure.id';
import { structureGlobalId } from '../../../structure/core/api/structure.global-id';
import { StructureCommandDispatcher } from '../../../structure/core/api/structure.command-dispatcher';
import { RowColoring } from './row-coloring';
import { FabricModalThemeService, Theme } from '@generic-ui/fabric';


@Injectable()
export class SchemaCommandInvoker implements CommandInvoker {

	constructor(private readonly schemaDispatcher: SchemaDispatcher,
				private readonly structureCommandService: StructureCommandDispatcher,
				private readonly fabricModalThemeService: FabricModalThemeService) {
	}

	create(schemaId: SchemaReadModelRootId = schemaGlobalId): void {
		this.schemaDispatcher.create(schemaId.toAggregateId());
	}

	setTheme(theme: SchemaTheme, schemaId: SchemaReadModelRootId = schemaGlobalId, structureId: StructureId = structureGlobalId): void {
		this.schemaDispatcher.setTheme(theme, schemaId.toAggregateId());
		this.fabricModalThemeService.changeTheme(this.toFabricTheme(theme)); // TODO github #2162
		this.structureCommandService.setRowHeightBasedOnTheme(theme, structureId);
	}

	setRowColoring(rowColoring: RowColoring, schemaId: SchemaReadModelRootId = schemaGlobalId): void {

		const schemaRowColoring: SchemaRowColoring = this.toSchemaRowColoring(rowColoring);

		this.schemaDispatcher.setRowColoring(schemaRowColoring, schemaId.toAggregateId());
	}

	setVerticalGrid(verticalGrid: boolean, schemaId: SchemaReadModelRootId = schemaGlobalId): void {
		this.schemaDispatcher.setVerticalGrid(verticalGrid, schemaId.toAggregateId());
	}

	setHorizontalGrid(horizontalGrid: boolean, schemaId: SchemaReadModelRootId = schemaGlobalId): void {
		this.schemaDispatcher.setHorizontalGrid(horizontalGrid, schemaId.toAggregateId());
	}

	private toSchemaRowColoring(coloring: RowColoring): SchemaRowColoring {
		switch (coloring) {

			case RowColoring.NONE:
				return SchemaRowColoring.NONE;

			case RowColoring.ODD:
				return SchemaRowColoring.ODD;

			case RowColoring.EVEN:
				return SchemaRowColoring.EVEN;
		}
	}

	private toFabricTheme(schemaTheme: SchemaTheme): Theme {

		switch (schemaTheme) {
			case SchemaTheme.DARK: {
				return Theme.DARK;
			}

			case SchemaTheme.FABRIC: {
				return Theme.FABRIC;
			}

			case SchemaTheme.GENERIC: {
				return Theme.GENERIC;
			}

			case SchemaTheme.LIGHT: {
				return Theme.LIGHT;
			}

			case SchemaTheme.MATERIAL: {
				return Theme.MATERIAL;
			}

		}
	}
}
