import { NgModule, Optional, Provider } from '@angular/core';
import { CommonModule } from '@angular/common';

import { FeatureModule } from '@generic-ui/hermes';

import { SummariesApiModule } from '../core/api/summaries.api-module';


@NgModule({
	imports: [
		CommonModule,
		SummariesApiModule
	],
	declarations: [],
	exports: []
})
export class SummariesFeatureModule extends FeatureModule {

	static forComponent(): Array<Provider> {
		return [];
	}

	constructor(@Optional() summariesApiModule: SummariesApiModule) {
		super();
		if (summariesApiModule === null) {
			throw new Error('SummariesApiModule is required.');
		}
	}

}
