import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Inject,
	OnInit,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';

import { StructureCommandDispatcher } from '../../../../core/api/structure.command-dispatcher';
import { CellTemplateWithContext } from '../../../../../composition/core/domain-read/definition/cell-template-with-context';
import { SmartComponent } from '../../../../../common/cdk/component/smart-component';
import { CompositionWarehouse } from '../../../../../composition/core/api/composition.warehouse';

import { StructureColumnMenuConfigArchive } from './config/structure.column-menu-config.archive';
import { StructureColumnMenuConfig } from './config/structure.column-menu-config';
import { FilterWarehouse } from '../../../../filter/core/api/filter.warehouse';
import { StructureId } from '../../../../core/api/structure.id';
import { UniqueValuesReadModel } from '../../../../filter/core/api/unique/unique-values.read-model';
import { TranslationService } from '../../../../../l10n/core/api/translation.service';
import { switchMap } from 'rxjs/operators';
import { Translation } from '../../../../../l10n/core/api/translation';


@Component({
	selector: 'gui-column-config',
	template: `
		<div *ngIf="isEnabled()"
			 class="gui-header-menu-tab">

			<gui-tab [menu]="config.getMenus()" [active]="config.getActiveMenu()">

				<ng-container *ngIf="config.isMainEnabled()">

					<gui-tab-item class="gui-tab-item-dropdown" [tab]="config.getMainMenu()">

						<gui-structure-column-config-sort *ngIf="column.isSortEnabled()"
														  [dropdownTextTranslation]="dropdownTextTranslation"
														  [column]="column">
						</gui-structure-column-config-sort>

						<gui-structure-column-config-column-hide
								[column]="column">
						</gui-structure-column-config-column-hide>

						<gui-structure-column-config-column-move
								[column]="column">
						</gui-structure-column-config-column-move>

					</gui-tab-item>

				</ng-container>

				<ng-container *ngIf="config.isFilteringEnabled()">

					<gui-tab-item [tab]="config.getFilterMenu()">

						<!--						<div class="gui-header-menu-item">{{config.getFilterMenu()}}</div>-->

						<div gui-unique-value-list
							 [fieldId]="column.getFieldId()">
						</div>

					</gui-tab-item>

				</ng-container>

				<ng-container *ngIf="config.isColumnManagerEnabled()">

					<gui-tab-item [tab]="config.getColumnMenu()">

						<div gui-structure-menu-column-manager>
						</div>

					</gui-tab-item>

				</ng-container>

			</gui-tab>
		</div>
	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnConfigComponent extends SmartComponent implements OnInit {

	@ViewChild('headerSortMenu', { static: false })
	headerSortMenu: ElementRef;

	config: StructureColumnMenuConfig;

	uniqueValues: Array<any> = [];

	hideColumnTitle: string;

	dropdownTextTranslation: string;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly structureCommandService: StructureCommandDispatcher,
				private readonly compositionReadModelService: CompositionWarehouse,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly translationService: TranslationService,
				private readonly structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				@Inject('column') public readonly column: CellTemplateWithContext) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.filterWarehouse
			.onUniqueValues(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((uniqueValuesReadModel: UniqueValuesReadModel) => {
				this.uniqueValues = uniqueValuesReadModel.getValues(this.column.getFieldId());
				this.changeDetectorRef.detectChanges();
			});

		this.structureColumnMenuConfigArchive
			.onValue()
			.pipe(
				switchMap((config: StructureColumnMenuConfig) => {
					this.config = config;
					return this.translationService.onTranslation();
				}),
				this.takeUntil()
			)
			.subscribe((translation: Translation) => {
				this.setTabTitles(translation);
				this.dropdownTextTranslation = translation.headerMenuMainTabColumnSort;
				this.changeDetectorRef.detectChanges();
			});
	}

	isEnabled(): boolean {
		return this.config && this.config.isEnabled();
	}

	private setTabTitles(translation: Translation): void {
		this.config.setMainMenu(translation.headerMenuMainTab);
		this.config.setFilterMenu(translation.headerMenuFilterTab);
		this.config.setColumnsMenu(translation.headerMenuColumnsTab);
	}

	protected getSelectorName(): string {
		return 'gui-column-config';
	}
}
