import { StructureId } from '../../../../core/api/structure.id';
import { SortOrder } from '../../../../../composition/core/domain/column/sort/sort-order';
import { FieldId } from '../../../../field/core/domain/field/field.id';
import { CompositionId } from '../../../../../composition/core/api/composition.id';
import { StructureCommand } from '../../../../core/domain/structure.command';


export class SetSortOrderCommand extends StructureCommand {

	constructor(structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly fieldId: FieldId,
				private readonly sortOrder: SortOrder) {
		super(structureId, 'SetSortOrderCommand');
	}

	getCompositionId(): CompositionId {
		return this.compositionId;
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getSortOrder(): SortOrder {
		return this.sortOrder;
	}
}
