import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	OnInit,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';

import { DomainEventBus } from '@generic-ui/hermes';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';

import { StructureHeaderColumnsComponent } from './column/structure-header-columns.component';
import { CellTemplateWithContext } from '../../../../composition/core/domain-read/definition/cell-template-with-context';
import { StructureId } from '../../../core/api/structure.id';
import { FilterWarehouse } from '../../../filter/core/api/filter.warehouse';
import { CompositionWarehouse } from '../../../../composition/core/api/composition.warehouse';
import { StructureHeaderFiltersComponent } from './filters/structure-header-filters.component';
import { StructureWarehouse } from '../../core/api/structure.warehouse';
import { StructureVerticalFormationWarehouse } from '../../../vertical-formation/core/api/structure-vertical-formation.warehouse';
import { RowSelectionType } from '../../../source/core/api/row-selection';
import { RowSelectionTypeArchive } from '../../../source/core/api/formation/type/row-selection-type.archive';
import { Group } from '../../../../composition/core/api/group/group';
import { CompositionId } from '../../../../composition/core/api/composition.id';
import { GroupCollection } from '../../../../composition/core/api/group/group.collection';
import { StyleModifier } from '../../../../common/cdk/dom/style/style-modifier';


@Component({
	selector: 'div[gui-structure-header]',
	templateUrl: './structure-header.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureHeaderComponent extends SmartComponent implements OnInit, AfterViewInit {

	@ViewChild(StructureHeaderColumnsComponent, { read: ElementRef, static: true })
	containerRef: ElementRef;

	@ViewChild(StructureHeaderFiltersComponent, { read: ElementRef, static: true })
	filtersRef: ElementRef;

	headerColumns: Array<CellTemplateWithContext> = [];

	filterRowEnabled: boolean = false;

	checkboxSelection: boolean = false;

	groups: Array<Group>;

	showGroups: boolean = false;

	private readonly styleModifier: StyleModifier;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly eventBus: DomainEventBus,
				private readonly structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly rowSelectionTypeArchive: RowSelectionTypeArchive,
				private readonly structureVerticalFormationWarehouse: StructureVerticalFormationWarehouse,
				private readonly compositionWarehouse: CompositionWarehouse) {
		super(changeDetectorRef, elementRef);
		this.styleModifier = new StyleModifier(elementRef.nativeElement);
	}

	ngOnInit() {

		this.rowSelectionTypeArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((type: RowSelectionType) => {
				this.checkboxSelection = type === RowSelectionType.CHECKBOX;
				this.reRender();
			});

		this.compositionWarehouse
			.onHeaderColumns()
			.pipe(
				this.takeUntil()
			)
			.subscribe((columns: Array<CellTemplateWithContext>) => {
				this.headerColumns = columns;
				this.reRender();
			});

		this.compositionWarehouse
			.onContainerWidth()
			.pipe(
				this.takeUntil()
			)
			.subscribe((width: number) => {
				this.styleModifier.getElement(this.containerRef.nativeElement).setWidth(width);
			});

		this.filterWarehouse
			.onFilteringEnabled(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {

				this.filterRowEnabled = enabled;
				this.reRender();
			});

		this.compositionWarehouse
			.onGroups(this.compositionId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((collection: GroupCollection) => {

				this.showGroups = collection.isVisible();
				this.groups = collection.getGroups();
				this.reRender();
			});
	}

	ngAfterViewInit() {
		super.ngAfterViewInit();

		this.structureVerticalFormationWarehouse
			.onRowHeight(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowHeight: number) => {

				const headerHeight = +(rowHeight) + 2;

				if (this.filtersRef) {
					this.styleModifier.getElement(this.filtersRef.nativeElement).setHeight(headerHeight);
				}
			});
	}

	protected getSelectorName(): string {
		return 'gui-structure-header';
	}

}
