import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { DomainEventBus, EventRepository } from '@generic-ui/hermes';

import { StructureId } from '../../../core/api/structure.id';
import { SearchPhraseSetDomainEvent } from '../domain/set-phrase/search-phrase-set.domain-event';
import { StructureReadModelRootId } from '../../../grid/core/api/read/structure.read-model-root-id';


@Injectable()
export class SearchEventRepository extends EventRepository<StructureReadModelRootId, StructureId> {

	constructor(domainEventBus: DomainEventBus) {
		super(domainEventBus);
	}

	onSearchPhrase(structureId: StructureReadModelRootId): Observable<string> {

		return this.onEvent(structureId, SearchPhraseSetDomainEvent)
				   .pipe(
					   filter((event: SearchPhraseSetDomainEvent) => event.isInitial() === false),
					   map((event: SearchPhraseSetDomainEvent) => event.getPhrase()),
					   filter((phrase: string) => phrase !== null)
				   );
	}
}
