import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';

import { CompositionWarehouse } from '../../../../composition/core/api/composition.warehouse';
import { CellTemplateWithContext } from '../../../../composition/core/domain-read/definition/cell-template-with-context';
import { CompositionCommandInvoker } from '../../../../composition/core/api/composition.command-invoker';
import { CompositionId } from '../../../../composition/core/api/composition.id';


@Component({
	selector: 'div[gui-structure-column-manager]',
	template: `

		<ol class="gui-p-0 gui-my-4 gui-mx-0 gui-list-none gui-overflow-auto">
			<li *ngFor="let column of columns"
				(click)="toggleColumn(column)"
				class="gui-p-6 gui-cursor-pointer">

				<gui-checkbox [checked]="column.isEnabled()"
							  [disabled]="enabledColumnsCount === 1 && column.isEnabled()">

					<ng-container
							*ngTemplateOutlet="column.viewTemplate;
											context: column.context">
					</ng-container>
				</gui-checkbox>

			</li>
		</ol>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnManagerComponent extends SmartComponent implements OnInit {

	columns: Array<CellTemplateWithContext>;

	enabledColumnsCount: number;

	constructor(private changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private compositionId: CompositionId,
				private compositionCommandInvoker: CompositionCommandInvoker,
				private compositionWarehouse: CompositionWarehouse) {
		super(changeDetectorRef, elementRef);

		this.addClassToHost('gui-block');
	}

	ngOnInit() {

		this.compositionWarehouse
			.onAllColumns(this.compositionId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((columns: Array<CellTemplateWithContext>) => {
				this.columns = columns;
				this.enabledColumnsCount = this.columns
											   .map((c) => +c.isEnabled())
											   .reduce((accumulator, currentValue) => accumulator + currentValue);

				this.changeDetectorRef.detectChanges();
			});
	}

	toggleColumn(column: CellTemplateWithContext): void {
		event.stopPropagation();
		if (column.isEnabled()) {
			this.compositionCommandInvoker.disableColumn(column.getColumnDefinitionId(), this.compositionId);
		} else {
			this.compositionCommandInvoker.enableColumn(column.getColumnDefinitionId(), this.compositionId);
		}
	}

	protected getSelectorName(): string {
		return 'gui-structure-column-manager';
	}
}
