import { Injectable } from '@angular/core';

import { PagingConverter } from '../../../../paging/core/api/read/paging.converter';
import { SourceConverter } from '../../../../source/core/api/source.converter';
import { VerticalFormationConverter } from '../../../../vertical-formation/core/api/vertical-formation.converter';
import { StructureAggregate } from '../../../../core/domain/structure.aggregate';
import { StructureReadModelRoot } from './structure.read-model-root';
import { OriginItemEntity } from '../../../../source/core/domain/origin/origin-item-entity';
import { ItemEntity } from '../../../../source/core/domain/core/item/item.entity';


@Injectable()
export class StructureReadModelRootConverter {

	constructor(private pagingConverter: PagingConverter,
				private sourceConverter: SourceConverter,
				private verticalFormationConverter: VerticalFormationConverter) {
	}

	convert(aggregate: StructureAggregate): StructureReadModelRoot {

		const pagingAggregate = aggregate.getPaging(),
			originItemEntities = aggregate.getEntities(),
			sourceCoordinator = aggregate.getSource(),
			verticalFormation = aggregate.getVerticalFormation();

		return new StructureReadModelRoot(
			aggregate.getId().toReadModelRootId(),
			this.pagingConverter.convert(pagingAggregate),
			this.convertSource(originItemEntities),
			this.sourceConverter.convert(sourceCoordinator),
			this.verticalFormationConverter.convert(verticalFormation)
		);
	}

	private convertSource(source: Array<OriginItemEntity>): Array<ItemEntity> {
		return source.map((originItem: OriginItemEntity) => {
			return new ItemEntity(originItem.rawData, originItem.getPosition(), originItem.getId().toString(), originItem.getVersion());
		});
	}
}
