import { ElementRef } from '@angular/core';

import { ClassModifier } from '../dom/class/class-modifier';
import { Changes } from './ng-changes';


export abstract class GuiComponent {

	private readonly innerClassModifier: ClassModifier = new ClassModifier(this.innerElementRef.nativeElement);

	protected constructor(private readonly innerElementRef: ElementRef) {
		this.addHostClass();
	}

	protected abstract getSelectorName(): string;

	addClassToHost(className: string): void {
		this.innerClassModifier.getHost().add(className);
	}

	removeClassFromHost(className: string): void {
		this.innerClassModifier.getHost().remove(className);
	}

	hasChanged<T>(prop: Changes<T>): boolean {
		return prop !== undefined && prop.currentValue !== undefined;
	}

	private addHostClass(): void {
		this.innerClassModifier.getHost().add(this.getSelectorName());
	}
}
