import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { FieldReadModel } from '../../../../field/core/api/read/field.read-model';
import { PureComponent } from '../../../../../common/cdk/component/pure-component';

@Component({
	selector: 'div[gui-field-selector][fields]',
	template: `

		Field:

		<gui-dropdown>
			<gui-dropdown-item *ngFor="let field of fields"
							   (click)="onSelectChange(field)">
				{{field.getName()}}
			</gui-dropdown-item>
		</gui-dropdown>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FieldSelectorComponent extends PureComponent {

	@Input()
	fields: ReadonlyArray<FieldReadModel>;

	@Output()
	fieldSelected = new EventEmitter<FieldReadModel>();

	constructor(elementRef: ElementRef) {
		super(elementRef);
	}

	onSelectChange(field: FieldReadModel): void {
		this.fieldSelected.emit(field);
	}

	protected getSelectorName(): string {
		return 'gui-field-selector';
	}
}
