import { Injectable } from '@angular/core';

import { CommandInvoker } from '@generic-ui/hermes';

import { StructureId } from '../../../core/api/structure.id';
import { structureGlobalId } from '../../../core/api/structure.global-id';
import { SearchConfig } from './search-config';
import { SearchDispatcher } from '../domain/search.dispatcher';


@Injectable()
export class SearchCommandInvoker implements CommandInvoker {

	constructor(private readonly searchDispatcher: SearchDispatcher) {
	}

	setSearchingConfig(config: SearchConfig, structureId: StructureId = structureGlobalId): void {
		this.searchDispatcher.setSearchingConfig(config, structureId);
	}

	search(phrase: string, structureId: StructureId = structureGlobalId): void {
		this.searchDispatcher.search(phrase, structureId);
	}
}
