import { Injectable } from '@angular/core';

import { DomainEventType, MultiDomainEventHandler } from '@generic-ui/hermes';

import { SortToggledEvent } from '../../../../../structure/sorting/core/domain/toggle/sort-toggled.event';

import { CompositionDispatcher } from '../../composition.dispatcher';
import { ColumnFieldId } from '../field/column-field.id';
import { ChangeSortParams } from './change-sort.params';
import { SortOrderSetEvent } from '../../../../../structure/sorting/core/domain/order/sort-order-set.event';
import { CompositionId } from '../../../api/composition.id';

export type CompositionChangeSortStatusEventType = SortToggledEvent | SortOrderSetEvent;

@Injectable()
export class CompositionChangeSortStatusEventHandler implements MultiDomainEventHandler<CompositionId, CompositionChangeSortStatusEventType> {

	constructor(private compositionDispatcher: CompositionDispatcher) {
	}

	forEvents(): Array<DomainEventType<CompositionChangeSortStatusEventType>> {
		return [
			SortToggledEvent,
			SortOrderSetEvent
		];
	}

	handle(event: CompositionChangeSortStatusEventType): void {

		if (event.ofMessageType('SortToggledEvent')) {

			const compositionId = event.getCompositionId(),
				dirs = event.getDirections();

			let params = dirs.map((dir: any) => {

				const columnFieldId = new ColumnFieldId(dir.fieldId.getId());

				return new ChangeSortParams(columnFieldId, dir.direction);
			});

			this.compositionDispatcher.changeSort(compositionId, params);
		}

		if (event.ofMessageType('SortOrderSetEvent')) {

			const compositionId = event.getCompositionId(),
				dirs = event.getDirections();

			let params = dirs.map((dir: any) => {

				const columnFieldId = new ColumnFieldId(dir.fieldId.getId());

				return new ChangeSortParams(columnFieldId, dir.direction);
			});

			this.compositionDispatcher.changeSort(compositionId, params);
		}
	}


}
