import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnChanges, ViewEncapsulation } from '@angular/core';

import { Paging } from '../../../core/api/read/paging';
import { PagingCommandInvoker } from '../../../core/api/paging.command-invoker';
import { NgChanges } from '../../../../../common/cdk/component/ng-changes';
import { SmartComponent } from '../../../../../common/cdk/component/smart-component';


@Component({
	selector: 'div[gui-paging-alternative-pages][paging]',
	templateUrl: `./alternative-paging-pages.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class AlternativePagingPagesComponent extends SmartComponent implements OnChanges {

	@Input()
	paging: Paging;

	@Input()
	sourceSize?: number = 0;

	currentPage: number;

	pages: Array<number>;

	numberOfVisiblePages: number = 3;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elRef: ElementRef,
				private pagingCommandService: PagingCommandInvoker) {
		super(changeDetectorRef, elRef);
	}

	ngOnChanges(changes: NgChanges<AlternativePagingPagesComponent>) {
		this.calculate();
	}

	calculate(): void {
		if (this.paging && this.sourceSize) {
			let numberOfPages = Math.ceil(this.sourceSize / this.paging.getPageSize());

			this.currentPage = this.paging.getPage();

			this.pages = [];
			if (this.pages.length <= numberOfPages) {
				for (let i = 1; i <= numberOfPages; i++) {
					this.pages.push(i);
				}
			}

		}
	}

	isSourceNotEmpty(): boolean {
		return this.sourceSize > 0;
	}

	goToPage(pageNumber: number) {
		const currentPage = this.paging.getPage();
		this.pagingCommandService.goToPage(pageNumber, currentPage);
	}

	calculateVisiblePages(page: number): boolean {
		return this.paging.calculateVisiblePages(this.currentPage, this.numberOfVisiblePages, page);
	}

	activePage(page: number): boolean {
		return this.currentPage === page;
	}

	protected getSelectorName(): string {
		return 'gui-paging-alternative-pages';
	}

}
