import { CompositionId } from '../../../api/composition.id';
import { ActiveColumnEntity } from '../active/active-column.entity';
import { CompositionDomainEvent } from '../../composition.domain-event';


export class CompositionChangeSortStatusEvent extends CompositionDomainEvent {

	constructor(aggregateId: CompositionId,
				private readonly activeColumns: Array<ActiveColumnEntity>) {
		super(aggregateId, 'CompositionChangeSortStatusEvent');
	}

	getCompositionId(): CompositionId {
		return this.getAggregateId();
	}

	getActiveColumns(): Array<ActiveColumnEntity> {
		return this.activeColumns;
	}

}
