import { EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output } from '@angular/core';

import { Gate } from '../../../../common/cdk/component/gate';
import { StructureId } from '../../../core/api/structure.id';
import { RowSelection } from '../../core/api/row-selection';
import { SelectedRow } from '../../core/api/formation/selected-row';
import { FormationEventRepository } from '../../core/api/formation/formation.event-repository';
import { FormationCommandInvoker } from '../../core/api/formation/formation.command-invoker';
import { RowSelectionTypeArchive } from '../../core/api/formation/type/row-selection-type.archive';
import { StreamCloser } from '../../../../common/cdk/reactive/stream-closer';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';

export abstract class SelectionGate extends Gate implements OnChanges, OnInit, OnDestroy {

	@Input()
	rowSelection: RowSelection;

	@Output()
	itemsSelected: EventEmitter<any> = new EventEmitter();

	@Output()
	selectedRows: EventEmitter<Array<SelectedRow>> = new EventEmitter();

	private readonly localStreamCloser = new StreamCloser();

	protected constructor(protected readonly structureId: StructureId,
						  protected readonly formationEventService: FormationEventRepository,
						  protected readonly formationCommandDispatcher: FormationCommandInvoker,
						  protected readonly rowSelectionTypeArchive: RowSelectionTypeArchive) {
		super();
	}

	ngOnChanges(changes: NgChanges<SelectionGate>) {

		if (this.isDefined('rowSelection', changes)) {
			if (this.rowSelection.isEnabledDefined()) {
				this.formationCommandDispatcher.setSelection(this.rowSelection.isEnabled(), this.structureId);
			}

			if (this.rowSelection.isTypeDefined()) {
				this.rowSelectionTypeArchive.next(this.rowSelection.getType());
			}

			if (this.rowSelection.isModeDefined()) {
				this.formationCommandDispatcher.changeMode(this.rowSelection.getMode(), this.structureId);
			}
		}
	}

	ngOnInit() {

		this.formationEventService
			.onItemSelected(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((items: Array<SelectedRow>) => {
				this.itemsSelected.emit(items.map(i => i.getData()));
				this.selectedRows.emit(items);
			});
	}

	ngOnDestroy() {
		this.localStreamCloser.unsubscribe();
	}

}
