import { NgModule, Optional, Provider } from '@angular/core';
import { CommonModule } from '@angular/common';

import { FeatureModule } from '@generic-ui/hermes';
import { FieldApiModule } from '../core/api/field.api-module';

@NgModule({
	imports: [
		CommonModule,
		FieldApiModule
	],
	declarations: [],
	exports: []
})
export class FieldFeatureModule extends FeatureModule {

	static forComponent(): Array<Provider> {
		return [];
	}

	constructor(@Optional() fieldApiModule: FieldApiModule) {
		super();
		if (fieldApiModule === null) {
			throw new Error('FieldApiModule is required.');
		}
	}

}
