import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { Warehouse } from '@generic-ui/hermes';

import { CompositionReadModelRootRepository } from '../domain-read/composition.read-model-root-repository';
import { CompositionId } from './composition.id';
import { compositionGlobalId } from './composition.global-id';
import { CompositionReadModeRoot } from '../domain-read/composition.read-mode-root';
import { CellTemplateWithContext } from '../domain-read/definition/cell-template-with-context';
import { CellTemplateWithAccessor } from '../domain-read/definition/cell-template-with-accessor';
import { SortOrder } from '../domain/column/sort/sort-order';
import { FieldId } from '../../../structure/field/core/domain/field/field.id';
import { GroupCollection } from './group/group.collection';
import { CompositionGroupRepository } from '../domain-read/group/composition.group.repository';


@Injectable()
export class CompositionWarehouse implements Warehouse {

	constructor(private readonly compositionRepository: CompositionReadModelRootRepository,
				private readonly compositionGroupRepository: CompositionGroupRepository) {
	}

	onWidth(compositionId: CompositionId = compositionGlobalId): Observable<number> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.getWidth())
				   );
	}

	onContainerWidth(compositionId: CompositionId = compositionGlobalId): Observable<number> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.getContainerWidth())
				   );
	}

	onWidthForEachColumn(compositionId: CompositionId = compositionGlobalId): Observable<Array<number>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.getAllColumns().map(c => c.width))
				   );
	}

	onHeaderColumns(compositionId: CompositionId = compositionGlobalId): Observable<Array<CellTemplateWithContext>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.getActiveHeaderColumns())
				   );
	}

	onAllColumns(compositionId: CompositionId = compositionGlobalId): Observable<Array<CellTemplateWithContext>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.getHeaderColumns())
				   );
	}

	onSortOrder(fieldId: FieldId, compositionId: CompositionId = compositionGlobalId): Observable<SortOrder> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.getActiveHeaderColumns()),
					   map((columns: Array<CellTemplateWithContext>) => {
						   return columns.filter((c) => c.getFieldId().equals(fieldId));
					   }),
					   filter((columns: Array<CellTemplateWithContext>) => {
						   return columns.length > 0;
					   }),
					   map(columns => columns[0].getSortStatus())
				   );
	}

	onTemplateColumns(compositionId: CompositionId = compositionGlobalId): Observable<Array<CellTemplateWithAccessor>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.getTemplateColumns())
				   );
	}

	onResizeWidth(compositionId: CompositionId = compositionGlobalId): Observable<boolean> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: CompositionReadModeRoot) => composition.isResizeWidthEnabled())
				   );
	}

	onGroups(compositionId: CompositionId = compositionGlobalId): Observable<GroupCollection> {
		return this.compositionGroupRepository.on(compositionId);
	}

}
