import { FilterTypeReadModel } from './filter-type.read-model';
import { FieldId } from '../../../../field/core/domain/field/field.id';

export class FilterTypeMap {

	private readonly map: Map<string, Array<FilterTypeReadModel>>; // = new WeakMap<FieldId, Array<FilterTypeReadModel>>();

	constructor(map: Map<string, Array<FilterTypeReadModel>>) {
		this.map = map;
	}

	getFilterTypes(fieldId: FieldId): Array<FilterTypeReadModel> {
		return this.map.get(fieldId.toString());
	}


}
