import { ChangeDetectorRef, ElementRef, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { ColumnConfig, MultiColumnConfig } from '../../../../composition/core/api/column/column.config';
import { SchemaTheme } from '../../../../schema/core/api/schema-theme';
import { SortingConfig } from '../../../sorting/core/api/sorting-config';
import { FilterConfig } from '../../../filter/core/api/filter-config';
import { QuickFiltersConfig } from '../../../filter/core/api/quick-filters.config';
import { EditemItemValues } from '../../../source/core/api/event/editem-item.values';
import { StructureId } from '../../../core/api/structure.id';
import { CompositionId } from '../../../../composition/core/api/composition.id';
import { StructureCommandDispatcher } from '../../../core/api/structure.command-dispatcher';
import { PagingCommandInvoker } from '../../../paging/core/api/paging.command-invoker';
import { PagingEventRepository } from '../../../paging/core/api/paging.event-repository';
import { SourceCommandInvoker } from '../../../source/core/api/source.command-invoker';
import { SourceEventService } from '../../../source/core/api/event/source-event.service';
import { CompositionCommandInvoker } from '../../../../composition/core/api/composition.command-invoker';
import { CompositionEventRepository } from '../../../../composition/core/api/composition.event-repository';
import { FormationEventRepository } from '../../../source/core/api/formation/formation.event-repository';
import { StructureEditModeArchive } from '../edit/structure.edit-mode.archive';
import { StructureCellEditArchive } from '../edit/structure.cell-edit.archive';
import { StructureInfoPanelArchive } from '../panel/info/structure.info-panel.archive';
import { StructureSummariesConfigService } from '../panel/summaries/structure.summaries-config.service';
import { StructureCellEditStore } from '../edit/structure.cell-edit.store';
import { StructureCellEditState } from '../edit/structure.cell-edit.state';
import { StructureColumnMenuConfigArchive } from '../header/menu/config/structure.column-menu-config.archive';
import { PagingDisplayModeArchive } from '../../../paging/feature/mode/paging-display-mode.archive';
import { RowSelectEnabledRepository } from '../../../source/core/api/formation/set-enabled/row-select-enabled.repository';
import { SearchEventRepository } from '../../../search/core/api/search.event-repository';
import { SchemaCommandInvoker } from '../../../../schema/core/api/schema.command-invoker';
import { StructureHeaderBottomEnabledArchive } from '../header/structure-header-bottom-enabled.archive';
import { StructureHeaderTopEnabledArchive } from '../header/structure-header-top-enabled.archive';
import { SchemaEventRepository } from '../../../../schema/core/api/schema.event-repository';
import { SchemaReadModelRootId } from '../../../../schema/core/domain-read/schema.read-model-root-id';
import { StructureRowDetailConfigArchive } from '../row-detail/structure.row-detail.config-archive';
import { StructureTitlePanelConfigArchive } from '../panel/banner-panels/title-panel/structure.title-panel.config-archive';
import { StructureFooterPanelConfigArchive } from '../panel/banner-panels/footer-panel/structure.footer-panel.config-archive';
import { StructureInfoPanelConfigService } from '../panel/info/structure.info-panel-config.service';
import { SortingCommandInvoker } from '../../../sorting/core/api/sorting.command-invoker';
import { SearchCommandInvoker } from '../../../search/core/api/search.command-invoker';
import { FieldCommandInvoker } from '../../../field/core/api/field.command-invoker';
import { CommandDispatcher, DomainEventBus } from '@generic-ui/hermes';
import { StructureColumnInputHandler } from './column/structure-column.input-handler';
import { ColumnFieldFactory } from '../../../../composition/core/domain/column/field/colum-field.factory';
import { RowSelectionTypeArchive } from '../../../source/core/api/formation/type/row-selection-type.archive';
import { FormationCommandInvoker } from '../../../source/core/api/formation/formation.command-invoker';
import { TranslationService } from '../../../../l10n/core/api/translation.service';
import { SchemaRowClassArchive } from '../../../../schema/core/api/styling/schema.row-class.archive';
import { SchemaRowStyleArchive } from '../../../../schema/core/api/styling/schema.row-style.archive';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';


/** @internal */
export abstract class StructureGateway extends SmartComponent implements OnChanges, OnInit {

	/***********************
	 * INPUTS
	 ***********************/

	@Input()
	maxHeight: number;

	@Input()
	width: number;

	@Input()
	rowHeight: number;

	@Input()
	autoResizeWidth: boolean;

	@Input()
	source: Array<any> = [];

	@Input()
	columns: Array<ColumnConfig | MultiColumnConfig> = [];

	@Input()
	verticalGrid: boolean;

	@Input()
	horizontalGrid: boolean;

	@Input()
	theme: SchemaTheme;

	// @Input()
	// rowColoring: RowColoring;

	// @Input()
	// rowStyle: GuiRowStyle;

	// @Input()
	// rowClass: GuiRowClass;

	@Input()
	loading: boolean;

	@Input()
	virtualScroll: boolean;

	@Input()
	sorting: boolean | SortingConfig;

	@Input()
	filtering: boolean | FilterConfig;

	@Input()
	quickFilters: boolean | QuickFiltersConfig;

	@Input()
	editMode: boolean;

	@Input()
	cellEditing: boolean;

	// @Input()
	// infoPanel: boolean | InfoPanelConfig;

	// @Input()
	// summaries: SummariesConfig;

	// @Input()
	// columnMenu: ColumnMenuConfig;
	//
	// @Input()
	// rowDetail: RowDetailConfig;

	// @Input()
	// titlePanel: TitlePanelConfig;
	//
	// @Input()
	// footerPanel: FooterPanelConfig;
	//
	// @Input()
	// localization: GuiLocalization;


	/***********************
	 * OUTPUTS
	 ***********************/

	@Output()
	columnsChanged: EventEmitter<void> = new EventEmitter();

	@Output()
	containerWidthChanged: EventEmitter<number> = new EventEmitter();

	@Output()
	sourceEdited: EventEmitter<EditemItemValues> = new EventEmitter();

	@Output()
	cellEditEntered: EventEmitter<void> = new EventEmitter();

	@Output()
	cellEditCanceled: EventEmitter<void> = new EventEmitter();

	@Output()
	cellEditSubmitted: EventEmitter<void> = new EventEmitter();

	@Output()
	themeChanged: EventEmitter<SchemaTheme> = new EventEmitter();

	@Output()
	horizontalGridChanged: EventEmitter<boolean> = new EventEmitter();

	@Output()
	verticalGridChanged: EventEmitter<boolean> = new EventEmitter();

	// @Output()
	// rowColoringChanged: EventEmitter<SchemaRowColoring> = new EventEmitter();

	structureColumnInputHandler: StructureColumnInputHandler;

	private changeAfterInit = false;

	protected constructor(
		protected readonly changeDetectorRef: ChangeDetectorRef,
		elementRef: ElementRef,
		protected readonly domainEventBus: DomainEventBus,
		protected readonly commandDispatcher: CommandDispatcher,
		public structureId: StructureId,
		protected compositionId: CompositionId,
		protected schemaId: SchemaReadModelRootId,
		protected structureCommandService: StructureCommandDispatcher,
		protected structurePagingCommandDispatcher: PagingCommandInvoker,
		protected pagingEventRepository: PagingEventRepository,
		protected sortingCommandDispatcher: SortingCommandInvoker,
		protected searchCommandDispatcher: SearchCommandInvoker,
		protected fieldCommandDispatcher: FieldCommandInvoker,
		protected sourceCommandService: SourceCommandInvoker,
		protected sourceEventService: SourceEventService,
		protected schemaCommandDispatcher: SchemaCommandInvoker,
		protected compositionCommandDispatcher: CompositionCommandInvoker,
		protected compositionEventService: CompositionEventRepository,
		protected formationEventService: FormationEventRepository,
		protected structureEditModeArchive: StructureEditModeArchive,
		protected structureCellEditArchive: StructureCellEditArchive,
		protected structureInfoPanelEnabledArchive: StructureInfoPanelArchive,
		protected structureInfoPanelConfigService: StructureInfoPanelConfigService,
		protected structureSummariesConfigService: StructureSummariesConfigService,
		protected structureCellEditStore: StructureCellEditStore,
		protected columnFieldFactory: ColumnFieldFactory,
		protected structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
		protected structurePagingDisplayModeArchive: PagingDisplayModeArchive,
		protected rowSelectEnabledArchive: RowSelectEnabledRepository,
		protected rowSelectionTypeArchive: RowSelectionTypeArchive,
		protected schemaRowClassArchive: SchemaRowClassArchive,
		protected schemaRowStyleArchive: SchemaRowStyleArchive,
		protected formationCommandDispatcher: FormationCommandInvoker,
		protected searchEventRepository: SearchEventRepository,
		protected structureHeaderTopEnabledArchive: StructureHeaderTopEnabledArchive,
		protected structureHeaderBottomEnabledArchive: StructureHeaderBottomEnabledArchive,
		protected structureDetailViewConfigArchive: StructureRowDetailConfigArchive,
		protected structureTitlePanelConfigArchive: StructureTitlePanelConfigArchive,
		protected structureFooterPanelConfigArchive: StructureFooterPanelConfigArchive,
		protected schemaEventRepository: SchemaEventRepository,
		protected translationService: TranslationService
	) {
		super(changeDetectorRef, elementRef);

		this.structureColumnInputHandler = new StructureColumnInputHandler(structureId,
			compositionId, commandDispatcher, domainEventBus, columnFieldFactory, compositionCommandDispatcher);

		this.translationService.setDefaultTranslation();
	}

	ngOnChanges(changes: NgChanges<StructureGateway>) {

		if (changes.theme !== undefined && changes.theme.currentValue !== undefined) {
			this.schemaCommandDispatcher.setTheme(this.theme);
		}

		// if (simpleChanges.rowDetail !== undefined && simpleChanges.rowDetail.currentValue !== undefined) {
		// 	this.structureDetailViewConfigArchive.next(this.rowDetail);
		// }

		// if (simpleChanges.titlePanel !== undefined && simpleChanges.titlePanel.currentValue !== undefined) {
		// 	this.structureTitlePanelConfigArchive.next(this.titlePanel);
		// }
		//
		// if (simpleChanges.footerPanel !== undefined && simpleChanges.footerPanel.currentValue !== undefined) {
		// 	this.structureFooterPanelConfigArchive.next(this.footerPanel);
		// }

		// if (simpleChanges.columnMenu !== undefined && simpleChanges.columnMenu.currentValue !== undefined) {
		// 	this.structureColumnMenuConfigArchive.nextConfig(this.columnMenu);
		// }

		// if (simpleChanges.infoPanel !== undefined && simpleChanges.infoPanel.currentValue !== undefined) {
		//
		// 	if (typeof this.infoPanel === 'boolean') {
		// 		this.infoPanel = {
		// 			enabled: this.infoPanel
		// 		};
		// 	}
		//
		// 	this.structureInfoPanelConfigService.set(this.infoPanel);
		// }

		// if (simpleChanges.summaries !== undefined && simpleChanges.summaries.currentValue !== undefined) {
		// 	this.structureSummariesConfigService.set(this.summaries);
		// }

		if (changes.editMode !== undefined && changes.editMode.currentValue !== undefined) {
			this.structureEditModeArchive.next(this.editMode);
		}

		if (changes.cellEditing !== undefined && changes.cellEditing.currentValue !== undefined) {
			this.structureCellEditArchive.next(this.cellEditing);
		}

		if (changes.width !== undefined && changes.width.currentValue !== undefined) {
			this.compositionCommandDispatcher.setWidth(this.width);
		}

		if (changes.rowHeight !== undefined && changes.rowHeight.currentValue !== undefined) {
			this.structureCommandService.setRowHeight(this.rowHeight);
		}

		if (changes.autoResizeWidth !== undefined && changes.autoResizeWidth.currentValue !== undefined) {
			this.compositionCommandDispatcher.setResizeWidth(this.autoResizeWidth);
		}

		if (changes.loading !== undefined && changes.loading.currentValue !== undefined) {
			this.sourceCommandService.setLoading(this.loading);
		}

		if (changes.columns !== undefined && changes.columns.currentValue !== undefined) {
			this.structureColumnInputHandler.handle(changes);
		}

		if (changes.verticalGrid !== undefined && changes.verticalGrid.currentValue !== undefined) {
			this.schemaCommandDispatcher.setVerticalGrid(this.verticalGrid);
		}

		if (changes.horizontalGrid !== undefined && changes.horizontalGrid.currentValue !== undefined) {
			this.schemaCommandDispatcher.setHorizontalGrid(this.horizontalGrid);
		}

		// if (simpleChanges.rowColoring !== undefined && simpleChanges.rowColoring.currentValue !== undefined) {
		// 	this.schemaCommandDispatcher.setRowColoring(this.rowColoring);
		// }

		// if (simpleChanges.rowClass !== undefined && simpleChanges.rowClass.currentValue !== undefined) {
		// 	this.schemaRowClassArchive.next(this.rowClass as SchemaRowClass);
		// }

		// if (simpleChanges.rowStyle !== undefined && simpleChanges.rowStyle.currentValue !== undefined) {
		// 	this.schemaRowStyleArchive.next(this.rowStyle as SchemaRowStyle);
		// }

		if (changes.maxHeight && this.maxHeight) {
			this.structureCommandService.setContainerHeight(this.maxHeight);
		}

		if (changes.virtualScroll !== undefined && changes.virtualScroll.currentValue !== undefined) {
			if (this.virtualScroll) {
				this.structureCommandService.enableVerticalScroll();
			} else {
				this.structureCommandService.disableVerticalScroll();
			}
		}

		if (changes.sorting !== undefined && changes.sorting.currentValue !== undefined) {

			let sorting: SortingConfig;

			if (typeof this.sorting === 'boolean') {
				sorting = {
					enabled: this.sorting
				};
			} else {
				sorting = this.sorting;
			}

			this.sortingCommandDispatcher.setSortingConfig(sorting, this.structureId);
		}

		if (changes.filtering !== undefined && changes.filtering.currentValue !== undefined) {

			let filtering: FilterConfig;

			if (typeof this.filtering === 'boolean') {
				filtering = {
					enabled: this.filtering
				};
			} else {
				filtering = this.filtering;
			}

			this.structureCommandService.setFilterConfig(filtering);
		}

		if (changes.quickFilters !== undefined && changes.quickFilters.currentValue !== undefined) {

			let quickFilters: QuickFiltersConfig;

			if (typeof this.quickFilters === 'boolean') {
				quickFilters = {
					enabled: this.quickFilters
				};
			} else {
				quickFilters = this.quickFilters;
			}

			this.structureCommandService.setQuickFiltersConfig(quickFilters);
		}

		// if (simpleChanges.localization !== undefined && simpleChanges.localization.currentValue !== undefined) {
		//
		// 	if (this.localization.translationResolver) {
		// 		this.translationService.setResolver(this.localization.translationResolver);
		// 	}
		//
		// 	if (this.localization.translation) {
		// 		this.translationService.changeTranslation(this.localization.translation);
		// 	}
		// }

		/**
		 * Setting source should be last step
		 */
		if (changes.source) {
			this.sourceCommandService.setOrigin(this.source);
		}
	}

	ngOnInit() {

		this.compositionEventService
			.onColumnsChanged(this.compositionId.toReadModelRootId())
			.pipe(
				this.takeUntil())
			.subscribe(() => {
				this.columnsChanged.emit();
			});

		this.compositionEventService
			.onContainerWidthChanged(this.compositionId.toReadModelRootId())
			.pipe(
				this.takeUntil()
			)
			.subscribe((containerWidth: number) => {
				this.containerWidthChanged.emit(containerWidth);
			});

		this.sourceEventService
			.onSourceEdited(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((values: EditemItemValues) => {
				this.sourceEdited.emit(values);
			});

		this.structureCellEditStore
			.onValue()
			.pipe(this.takeUntil())
			.subscribe((state: StructureCellEditState) => {

				switch (state) {

					case StructureCellEditState.ENTER:
						this.cellEditEntered.emit();
						break;

					case StructureCellEditState.SUBMIT:
						this.cellEditSubmitted.emit();
						break;

					case StructureCellEditState.CANCEL:
						this.cellEditCanceled.emit();
						break;
				}

			});

		this.connectSchemaEvents();

		this.componentInitialized();
	}

	private connectSchemaEvents(): void {

		this.schemaEventRepository
			.onThemeChanged(this.schemaId)
			.pipe(this.takeUntil())
			.subscribe((theme: SchemaTheme) => {
				this.themeChanged.emit(theme);
			});

		this.schemaEventRepository
			.onHorizontalGridChanged(this.schemaId)
			.pipe(this.takeUntil())
			.subscribe((enabled: boolean) => {
				this.horizontalGridChanged.emit(enabled);
			});

		this.schemaEventRepository
			.onVerticalGridChanged(this.schemaId)
			.pipe(this.takeUntil())
			.subscribe((enabled: boolean) => {
				this.verticalGridChanged.emit(enabled);
			});

		// this.schemaEventRepository
		// 	.onRowColoring(this.schemaId)
		// 	.pipe(this.takeUntil())
		// 	.subscribe((coloring: SchemaRowColoring) => {
		// 		this.rowColoringChanged.emit(coloring);
		// 	});
	}

	private isInitialized(): boolean {
		return this.changeAfterInit;
	}

	private isNotInitialized(): boolean {
		return this.changeAfterInit;
	}

	private componentInitialized(): void {
		this.changeAfterInit = true;
	}

}
