import { Injectable } from '@angular/core';

import { Override } from '../../../../../common/cdk/decorators';

import { CompositionCommandInvoker } from '../../../../../composition/core/api/composition.command-invoker';
import { CompositionId } from '../../../../../composition/core/api/composition.id';
import { CompositionDispatcher } from '../../../../../composition/core/domain/composition.dispatcher';
import { compositionGlobalId } from '../../../../../composition/core/api/composition.global-id';
import { ColumnParams } from '../../../../../composition/core/domain/column/set-columns/column.params';
import { ColumnDefinitionId } from '../../../../../composition/core/domain-read/definition/column-definition-id';
import { CompositionGroupRepository } from '../../../../../composition/core/domain-read/group/composition.group.repository';


@Injectable()
export class LocalCompositionCommandDispatcher extends CompositionCommandInvoker {

	constructor(private readonly compositionId: CompositionId,
				compositionDispatcher: CompositionDispatcher,
				compositionGroupRepository: CompositionGroupRepository) {
		super(compositionDispatcher, compositionGroupRepository);
	}

	@Override
	createComposition(compositionId: CompositionId = compositionGlobalId): void {
		super.createComposition(this.compositionId);
	}

	@Override
	setColumns(params: Array<ColumnParams>): void {
		super.setColumns(params, this.compositionId);
	}

	@Override
	setGroups(configs: any, compositionId: CompositionId = compositionGlobalId): void {
		super.setGroups(configs, this.compositionId);
	}

	@Override
	setWidth(width: number, compositionId: CompositionId = compositionGlobalId): void {
		super.setWidth(width, this.compositionId);
	}

	@Override
	setContainerWidth(width: number, compositionId: CompositionId = compositionGlobalId): void {
		super.setContainerWidth(width, this.compositionId);
	}

	@Override
	setResizeWidth(enabled: boolean, compositionId: CompositionId = compositionGlobalId): void {
		super.setResizeWidth(enabled, this.compositionId);
	}

	@Override
	enableColumn(columnDefId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		super.enableColumn(columnDefId, this.compositionId);
	}

	@Override
	disableColumn(columnDefId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		super.disableColumn(columnDefId, this.compositionId);
	}

	@Override
	moveLeft(columnDefId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		super.moveLeft(columnDefId, this.compositionId);
	}

	@Override
	moveRight(columnDefId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		super.moveRight(columnDefId, this.compositionId);
	}

}
