import { Filter } from './filter';
import { OriginItemEntity } from '../../../source/core/domain/origin/origin-item-entity';
import { FilterSettings } from './filter.settings';
import { Field } from '../../../field/core/domain/field/field';
import { FilterTypeId } from './type/filter-type.id';
import { FieldId } from '../../../field/core/domain/field/field.id';
import { FilterTypeManager } from './type/filter-type.manager';
import { FilterTypeReadModel } from '../api/type/filter-type.read-model';
import { BaseFilterType } from './type/base-filter-type';
import { FilterIdGenerator } from '../api/filter.id-generator';
import { ActiveFilterReadModel } from '../api/active/active-filter.read-model';
import { FilterId } from '../api/filter.id';


export class FilterManager {

	private readonly filterSettings = new FilterSettings();

	// string -> FilterId
	private readonly filters = new Map<string, Filter>();

	private activeFilters: Array<Filter> = [];

	private filterTypeManager: FilterTypeManager;

	constructor() {
	}

	getSettings(): FilterSettings {
		return this.filterSettings;
	}

	getAll(): Array<Filter> {
		return Array.from(this.filters)
					.map((arr) => arr[1]);
	}

	getAllActiveFilters(fields: Map<string, Field>): ReadonlyArray<ActiveFilterReadModel> {
		return this.activeFilters
				   .map((af: Filter) => {
					   return new ActiveFilterReadModel(
						   af.getFilterId(),
						   (fields.get(af.getFieldId().toString())).getName(),
						   this.filterTypeManager.getFilterType(af.getFilterTypeId()).getName(),
						   af.getFilterValue()
					   );
				   });
	}

	getFilterTypes(): Map<string, Array<FilterTypeReadModel>> {
		return this.filterTypeManager.getFieldIdsToFilterTypes();
	}

	assignFilterTypes(fields: Array<Field>) {
		this.filterTypeManager = new FilterTypeManager(fields);
	}

	add(fieldId: FieldId, filterTypeId: FilterTypeId, value: any): void {

		const filter = new Filter(FilterIdGenerator.generateId(), fieldId, filterTypeId, value);

		this.activeFilters.push(filter);
	}

	filter(entities: Array<OriginItemEntity>, fields: Map<string, Field>): Array<OriginItemEntity> {

		let filteredEntities: Array<OriginItemEntity> = [...entities];

		for (let filter of this.activeFilters) {

			const filterTypeId = filter.getFilterTypeId(),
				filterType = this.getFilterType(filterTypeId),
				value = filter.getFilterValue();

			filteredEntities = filterType.filterMany(filteredEntities, fields.get(filter.getFieldId().toString()), value);
		}

		return filteredEntities;
	}

	removeAll(): void {
		this.activeFilters.length = 0;
	}

	remove(filterId: FilterId): void {

		this.activeFilters =
			this.activeFilters.filter((filter: Filter) => {
				return filter.getFilterId() !== filterId;
			});
	}

	private getFilterType(filterTypeId: FilterTypeId): BaseFilterType<any> {
		return this.filterTypeManager.getFilterType(filterTypeId);
	}


}
