import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { DomainEventBus, EventRepository } from '@generic-ui/hermes';

import { CompositionId } from './composition.id';
import { CompositionColumnsSetEvent } from '../domain/column/set-columns/composition.columns-set.event';
import { CompositionContainerWidthSetEvent } from '../domain/width/set-container-width/composition-container-width-set.event';
import { CompositionReadModelRootId } from '../domain-read/composition.read-model-root-id';


@Injectable()
export class CompositionEventRepository extends EventRepository<CompositionReadModelRootId, CompositionId> {

	constructor(domainEventBus: DomainEventBus) {
		super(domainEventBus);
	}

	onColumnsChanged(compositionId: CompositionReadModelRootId): Observable<CompositionColumnsSetEvent> {

		return this.onEvent(compositionId, CompositionColumnsSetEvent);
	}

	onContainerWidthChanged(compositionId: CompositionReadModelRootId): Observable<number> {

		return this.onEvent(compositionId, CompositionContainerWidthSetEvent)
				   .pipe(
					   map((event: CompositionContainerWidthSetEvent) => {
						   return event.getPayload();
					   }),
					   filter((width: number) => !!width)
				   );
	}

}
