import { SchemaRowColoring } from '../../api/schema-row-coloring';
import { SchemaId } from '../../api/schema.id';
import { SchemaCommand } from '../schema.command';


export class SetRowColoringCommand extends SchemaCommand {

	constructor(schemaId: SchemaId,
				private readonly coloring: SchemaRowColoring) {
		super(schemaId, 'SetRowColoringCommand');
	}

	getColoring(): SchemaRowColoring {
		return this.coloring;
	}

}
