import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../../core/api/structure.id';
import { FilterTypeReadModel } from '../../../api/type/filter-type.read-model';
import { FilterTypesInitedEvent } from './filter-types-inited.event';
import { StructureAggregateEvent } from '../../../../../core/domain/structure.aggregate-event';


export class FilterTypesInitedAggregateEvent extends StructureAggregateEvent {

	constructor(structureId: StructureId,
				private readonly filterTypes: Map<string, Array<FilterTypeReadModel>>) {
		super(structureId, 'FilterTypesInitedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {

		return new FilterTypesInitedEvent(
			this.getAggregateId(),
			this.filterTypes
		);
	}

	getFilterTypes(): Map<string, Array<FilterTypeReadModel>> {
		return this.filterTypes;
	}
}
