import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Warehouse } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/api/structure.id';
import { structureGlobalId } from '../../../../core/api/structure.global-id';
import { RowSelectedRepository } from './row-selected/row-selected.repository';
import { RowSelectedReadModel } from './row-selected.read-model';
import { RowSelectionModeRepository } from './mode/row-selection-mode.repository';
import { RowSelectionMode } from '../row-selection';
import { SelectedRow } from './selected-row';
import { SourceWarehouse } from '../source.warehouse';


@Injectable()
export class FormationWarehouse implements Warehouse {

	constructor(private readonly rowSelectedRepository: RowSelectedRepository,
				private readonly rowSelectionModeRepository: RowSelectionModeRepository,
				private readonly sourceWarehouse: SourceWarehouse) {
	}

	getSelectedRows(structureId: StructureId = structureGlobalId): Array<SelectedRow> {

		const items = this.sourceWarehouse.getPreparedEntities(structureId),
			selectedItemIds = this.getSelectedItemIds(structureId);

		let selectedItems = [],
			length = items.length;

		for (let i = 0; i < length; i += 1) {

			let item = items[i];

			if (selectedItemIds.some((itemId: string) => itemId === item.getId().toString())) {
				selectedItems.push(new SelectedRow(item.getData(), i, item.getId()));
			}
		}
		return selectedItems;
	}

	onRowSelectedReadModel(structureId: StructureId = structureGlobalId): Observable<RowSelectedReadModel> {
		return this.rowSelectedRepository
				   .on(structureId);
	}

	getSelectedItemIds(structureId: StructureId = structureGlobalId): Array<string> {
		return this.rowSelectedRepository.get(structureId).getAll();
	}

	onSelectedRows(structureId: StructureId = structureGlobalId): Observable<Array<string>> {
		return this.rowSelectedRepository
				   .on(structureId)
				   .pipe(
					   map((rm: RowSelectedReadModel) => {
						   return rm.getAll();
					   })
				   );
	}

	onMode(structureId: StructureId = structureGlobalId): Observable<RowSelectionMode> {
		return this.rowSelectionModeRepository.on(structureId);
	}

}
