import { Injectable } from '@angular/core';

import { AggregateFactory } from '@generic-ui/hermes';

import { PagingManagerFactory } from '../../paging/core/domain/paging.manager-factory';
import { SourceManagerFactory } from '../../source/core/domain/core/source.manager-factory';
import { VerticalFormationFactory } from '../../vertical-formation/core/domain/vertical-formation.factory';
import { SummariesManagerFactory } from '../../summaries/core/domain/summaries.manager.factory';
import { FilterManagerFactory } from '../../filter/core/domain/filter.manager-factory';
import { FieldCollectionFactory } from '../../field/core/domain/field-collection.factory';
import { StructureId } from '../api/structure.id';
import { StructureAggregate } from './structure.aggregate';
import { SorterCollection } from '../../sorting/core/domain/sorter.collection';
import { SearchManagerFactory } from '../../search/core/domain/search.manager-factory';
import { UniqueFilterManager } from '../../filter/core/domain/unique/unique-filter-manager';


@Injectable()
export class StructureAggregateFactory extends AggregateFactory<StructureId, StructureAggregate> {

	constructor(private readonly pagingAggregateFactory: PagingManagerFactory,
				private readonly sourceManagerFactory: SourceManagerFactory,
				private readonly verticalFormationFactory: VerticalFormationFactory,
				private readonly summariesManagerFactory: SummariesManagerFactory,
				private readonly filterManagerFactory: FilterManagerFactory,
				private readonly searchManagerFactory: SearchManagerFactory,
				private readonly fieldCollectionFactory: FieldCollectionFactory) {
		super();
	}

	create(structureId: StructureId): StructureAggregate {

		const paging = this.pagingAggregateFactory.createDefault(),
			source = this.sourceManagerFactory.createDefault(),
			verticalFormation = this.verticalFormationFactory.create(structureId);

		const sorterContainer: SorterCollection = new SorterCollection(),
			filterContainer = this.filterManagerFactory.create(false),
			fieldContainer = this.fieldCollectionFactory.create(),
			summariesManager = this.summariesManagerFactory.create(structureId),
			searchManager = this.searchManagerFactory.create();

		const structureAggregate = new StructureAggregate(structureId,
			paging,
			source,
			verticalFormation,
			summariesManager,
			sorterContainer,
			filterContainer,
			new UniqueFilterManager(),
			searchManager,
			fieldContainer
		);

		this.init(structureAggregate);

		return structureAggregate;
	}

	init(structureAggregate: StructureAggregate): void {
		structureAggregate.init();
	}

}
