import { UniqueValue } from './unique-value';
import { UniqueValueIdGenerator } from './unique-value.id-generator';
import { UniqueValueId } from './unique-value-id';
import { Field } from '../../../../field/core/domain/field/field';

export class UniqueValueCollection {

	private readonly values: Array<UniqueValue> = [];

	private allSelected: boolean;

	private allDisabled: boolean;

	constructor(values: Array<any>,
				field: Field) {

		for (let value of values) {

			this.values.push(
				new UniqueValue(
					UniqueValueIdGenerator.generate(),
					value,
					field.getDisplayValue(value),
					true
				)
			);
		}

		this.calculateAllSelected();
		this.calculateAllDisabled();
	}

	getAll(): Array<UniqueValue> {
		return this.values;
	}

	isAllSelected(): boolean {
		return this.allSelected;
	}

	isAllDisabled(): boolean {
		return this.allDisabled;
	}

	getNotSelected(): Array<UniqueValue> {
		return this.values.filter((uv: UniqueValue) => uv.isDisabled());
	}

	selectAll(): void {
		this.values.forEach((uv: UniqueValue) => {
			uv.select();
		});
		this.allSelected = true;
		this.allDisabled = false;
	}

	select(uniqueValueId: UniqueValueId): void {

		this.values
			.filter((uv) => uv.getId().equals(uniqueValueId))
			.forEach((uv: UniqueValue) => {
				uv.select();
			});

		this.calculateAllSelected();
		this.calculateAllDisabled();
	}

	unselectAll(): void {
		this.values.forEach((uv: UniqueValue) => {
			uv.unselect();
		});
		this.allSelected = false;
		this.allDisabled = true;
	}

	unselect(uniqueValueId: UniqueValueId): void {

		this.values
			.filter((uv) => uv.getId().equals(uniqueValueId))
			.forEach((uv: UniqueValue) => {
				uv.unselect();
			});

		this.calculateAllSelected();
		this.calculateAllDisabled();
	}

	private calculateAllSelected(): void {
		this.allSelected = !this.values.some((f: UniqueValue) => f.isDisabled());
	}

	private calculateAllDisabled(): void {
		this.allDisabled = !this.values.some((f: UniqueValue) => f.isEnabled());
	}

}
