import { Injectable } from '@angular/core';

import { AggregateEvent, CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { CompositionAggregateRepository } from '../../composition.aggregate-repository';
import { CompositionEventConverter } from '../../composition.event-converter';
import { CompositionAggregate } from '../../composition.aggregate';

import { SetGroupsCommand } from './set-groups.command';
import { CompositionId } from '../../../api/composition.id';
import { ColumnConfig, MultiColumnConfig } from '../../../api/column/column.config';


@Injectable()
export class SetGroupsCommandHandler implements CommandHandler<CompositionAggregate, SetGroupsCommand> {

	constructor(private readonly compositionAggregateRepository: CompositionAggregateRepository,
				private readonly domainEventPublisher: DomainEventPublisher,
				private readonly compositionEventConverter: CompositionEventConverter) {
	}

	forCommand(): CommandType<SetGroupsCommand> {
		return SetGroupsCommand;
	}

	publishDomainEvents(aggregate: CompositionAggregate, command: SetGroupsCommand): void {
		this.publishEvents(aggregate, command);
	}

	handleAggregate(aggregate: CompositionAggregate, command: SetGroupsCommand): void {

		const groupConfigs: Array<ColumnConfig | MultiColumnConfig> = command.getConfigs();

		aggregate.setGroups(groupConfigs);
	}

	private publishEvents(composition: CompositionAggregate, command: SetGroupsCommand): void {

		const events = composition.getEvents() as Array<AggregateEvent<CompositionId>>;

		const domainEvents = this.compositionEventConverter.convert(events);

		this.domainEventPublisher.publish(domainEvents);
	}
}
