import { ChangeDetectionStrategy, Component, ElementRef, Input, OnChanges, ViewEncapsulation } from '@angular/core';

import { PureComponent } from '../../../../../common/cdk/component/pure-component';
import { Paging } from '../../../core/api/read/paging';


@Component({
	selector: 'div[gui-paging-stats][paging]',
	templateUrl: `./paging-stats.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class PagingStatsComponent extends PureComponent implements OnChanges {

	@Input()
	paging: Paging;

	sourceSize: number;

	firstItemIndex: number;

	lastItemIndex: number;

	constructor(elementRef: ElementRef) {
		super(elementRef);
		this.addClassToHost('gui-mx-6');
	}

	ngOnChanges() {
		this.calculate();
	}

	calculate(): void {
		if (this.paging) {
			this.firstItemIndex = this.paging.getStart();
			this.lastItemIndex = this.paging.getEnd();
			this.sourceSize = this.paging.getSourceSize();
		}
	}

	isSourceNotEmpty(): boolean {
		return this.sourceSize > 0;
	}

	protected getSelectorName(): string {
		return 'gui-paging-stats';
	}

}
