import { GenericBuilder } from '../../../../common/cdk/generic-builder';

export enum RowSelectionType {
	ROW,
	CHECKBOX
}

export enum RowSelectionMode {
	SINGLE,
	MULTIPLE
}

export class RowSelection {

	private readonly enabled: boolean;

	private readonly type: RowSelectionType;

	private readonly mode: RowSelectionMode;

	private constructor(config: {
		enabled?: boolean,
		mode?: RowSelectionMode,
		type?: RowSelectionType
	}) {
		if (config.enabled !== undefined) {
			this.enabled = config.enabled;
		}

		if (config.mode !== undefined) {
			this.mode = config.mode;
		}

		if (config.type !== undefined) {
			this.type = config.type;
		}
	}

	isModeDefined(): boolean {
		return this.mode !== undefined;
	}

	isTypeDefined(): boolean {
		return this.type !== undefined;
	}

	isEnabledDefined(): boolean {
		return this.enabled !== undefined;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	getMode(): RowSelectionMode {
		return this.mode;
	}

	getType(): RowSelectionType {
		return this.type;
	}

	static Builder = class RowSelectionBuilder extends GenericBuilder<RowSelection> {

		enabled: boolean;

		type: RowSelectionType;

		mode: RowSelectionMode;

		constructor() {
			super();
		}

		buildObject(): RowSelection {
			return new RowSelection({
				enabled: this.enabled,
				type: this.type,
				mode: this.mode
			});
		}

		withEnabled(enabled: boolean): RowSelectionBuilder {
			this.enabled = enabled;
			return this;
		}

		withType(type: RowSelectionType): RowSelectionBuilder {
			this.type = type;
			return this;
		}

		withMode(mode: RowSelectionMode): RowSelectionBuilder {
			this.mode = mode;
			return this;
		}

	};

}
