import { SchemaId } from '../../api/schema.id';
import { SchemaRowColoring } from '../../api/schema-row-coloring';
import { SchemaDomainEvent } from '../schema.domain-event';


export class RowColoringSetEvent extends SchemaDomainEvent {

	constructor(schemaId: SchemaId,
				private readonly rowColoring: SchemaRowColoring) {
		super(schemaId, 'RowColoringSetEvent');
	}

	getRowColoring(): SchemaRowColoring {
		return this.rowColoring;
	}
}
