import { FilterId } from '../api/filter.id';
import { FieldId } from '../../../field/core/domain/field/field.id';
import { FilterTypeId } from './type/filter-type.id';

export class Filter {

	private readonly filterId: FilterId;

	private readonly fieldId: FieldId;

	private readonly filterTypeId: FilterTypeId;

	private readonly filterValue: any;

	constructor(filterId: FilterId,
				fieldId: FieldId,
				filterTypeId: FilterTypeId,
				filterValue: any) { // TODO not any
		this.filterId = filterId;
		this.fieldId = fieldId;
		this.filterTypeId = filterTypeId;
		this.filterValue = filterValue;
	}

	getFilterId(): FilterId {
		return this.filterId;
	}

	getFilterTypeId(): FilterTypeId {
		return this.filterTypeId;
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getFilterValue(): any {
		return this.filterValue;
	}

}
