import { SchemaId } from '../../api/schema.id';
import { SchemaTheme } from '../../api/schema-theme';
import { SchemaDomainEvent } from '../schema.domain-event';

export class SchemaThemeSetEvent extends SchemaDomainEvent {

	constructor(schemaId: SchemaId,
				private readonly theme: SchemaTheme) {
		super(schemaId, 'SchemaThemeSetEvent');
	}

	getTheme(): SchemaTheme {
		return this.theme;
	}
}
