import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { AggregateId, Warehouse } from '@generic-ui/hermes';

import { StructureReadModelRepository } from '../../../grid/core/api/read/structure.read-model-repository';
import { structureGlobalId } from '../../../core/api/structure.global-id';
import { StructureReadModelRoot } from '../../../grid/core/api/read/structure.read-model-root';


@Injectable()
export class SortingWarehouse implements Warehouse {

	constructor(private readonly structureRepository: StructureReadModelRepository) {
	}

	onStructure(aggregateId: AggregateId = structureGlobalId): Observable<StructureReadModelRoot> {
		return this.structureRepository.onStructure(aggregateId);
	}

}
