import { FieldId } from '../../../../field/core/domain/field/field.id';
import { UniqueValueReadModel } from './unique-value.read-model';

export class UniqueValuesReadModel {

	private readonly map = new Map<string, Array<UniqueValueReadModel>>();

	private readonly allSelected: Map<string, boolean> = new Map<string, boolean>();

	private readonly allDisabled: Map<string, boolean> = new Map<string, boolean>();

	constructor(map: Map<string, Array<UniqueValueReadModel>>) {
		this.map = map;
		this.calculateSelection();
	}

	getValues(fieldId: FieldId): Array<UniqueValueReadModel> {
		return this.map.get(fieldId.toString());
	}

	areAllSelected(fieldId: FieldId): boolean {
		return this.allSelected.get(fieldId.toString());
	}

	areAllDisabled(fieldId: FieldId): boolean {
		return this.allDisabled.get(fieldId.toString());
	}

	isSelectAllChecked(fieldId: FieldId): boolean {
		return this.areAllSelected(fieldId);
	}

	isIndeterminate(fieldId: FieldId): boolean {
		return !(this.areAllSelected(fieldId) || this.areAllDisabled(fieldId));
	}

	private calculateSelection(): void {

		for (let key of Array.from(this.map.keys())) {

			const values = this.map.get(key);

			this.allSelected.set(
				key,
				!values.some((r) => !r.isEnabled())
			);

			this.allDisabled.set(
				key,
				!values.some((r) => r.isEnabled())
			);
		}
	}
}
