import { AggregateEvent, RandomStringGenerator } from '@generic-ui/hermes';

import { OriginItemEntity } from '../origin/origin-item-entity';
import { StructureId } from '../../../../core/api/structure.id';
import { StructureSourceItemEditedAggregateEvent } from '../origin/edit/structure.source-item-edited.aggregate-event';
import { StructureOriginChangedAggregateEvent } from '../origin/structure.origin-changed.aggregate-event';
import { Field } from '../../../../field/core/domain/field/field';
import { OriginId } from '../origin/origin-id';
import { FormationManager } from '../formation/core/formation-manager';
import { RowSelectToggleType } from '../formation/core/row-select-toggle-type';


export class SourceManager {

	private loading: boolean = false;

	private fetched: boolean = false;

	private origin: Array<OriginItemEntity>;

	private entities: Array<OriginItemEntity> = [];

	// Entities after filtering, searching
	private preparedEntities: Array<OriginItemEntity> = [];

	private slicedEntities: Array<OriginItemEntity> = [];

	private readonly formationManager: FormationManager;

	constructor(formationManager: FormationManager) {
		this.formationManager = formationManager;
	}

	isLoading(): boolean {
		return this.loading;
	}

	isFetched(): boolean {
		return this.fetched;
	}

	setLoading(enabled: boolean): void {
		this.loading = enabled;
	}

	setEntities(ent: Array<OriginItemEntity>): void {
		this.entities = ent;
		this.recalculatePositions();
	}

	getEntities(): Array<OriginItemEntity> {
		return this.entities;
	}

	setPreparedEntities(): void {
		this.preparedEntities = [...this.entities];
	}

	getPreparedEntities(): Array<OriginItemEntity> {
		return this.preparedEntities;
	}

	setSlicedEntities(ent: Array<OriginItemEntity>) {
		this.slicedEntities = ent;
	}

	getSlicedEntities(): Array<OriginItemEntity> {
		return this.slicedEntities;
	}

	setOrigin(items: Array<any> = [], structureId: StructureId): AggregateEvent<StructureId> {
		this.origin = this.convertItems(items);

		return this.createOriginChangedEvent(structureId);
	}

	setConvertedOrigin(items: Array<OriginItemEntity> = [], structureId: StructureId): AggregateEvent<StructureId> {
		this.origin = [...items];

		return this.createOriginChangedEvent(structureId);
	}

	getOrigin(): Array<OriginItemEntity> {
		return this.origin;
	}

	getOriginSize(): number {
		return this.origin.length;
	}

	// editOriginItemByProperty(byField, value: any, field: Field, structureId: StructureId): AggregateEvent {
	//
	// }

	editOriginItem(itemId: string, value: any, field: Field, structureId: StructureId): ReadonlyArray<AggregateEvent<StructureId>> {

		const item: OriginItemEntity = this.findOriginItem(itemId),
			itemBeforeChange = item.clone();

		if (item) {
			item.rawData[field.getAccessor()] = value;
			item.bumpVersion();
		}

		return [
			new StructureSourceItemEditedAggregateEvent(structureId, itemBeforeChange, item.clone()),
			this.createOriginChangedEvent(structureId)
		];
	}

	deleteAllSelected(structureId: StructureId): Array<AggregateEvent<StructureId>> {
		let events: Array<AggregateEvent<StructureId>> = [];

		const rows = this.formationManager.getSelectedRows();

		rows.forEach((r: string) => {
			events.push(...this.deleteOriginItemByItemId(new OriginId(r), structureId));
		});

		return events;
	}

	deleteOriginItem(payload: number | OriginId | Array<number> | Array<OriginId>, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		if (payload instanceof OriginId) {
			return this.deleteOriginItemByItemId(payload, structureId);
		} else if (Array.isArray(payload)) {

			let events: Array<AggregateEvent<StructureId>> = [];

			payload.forEach((item: number | OriginId) => {
				events.push(...this.deleteOneOriginItem(item, structureId));
			});

			return events;

		} else if (Number.isInteger(payload)) {
			return this.deleteOriginItemByIndex(payload, structureId);
		}
	}

	private deleteOneOriginItem(item: number | OriginId, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		if (item instanceof OriginId) {
			return this.deleteOriginItemByItemId(item, structureId);
		} else if (Number.isInteger(item)) {
			return this.deleteOriginItemByIndex(item, structureId);
		} else {
			return [];
		}
	}

	private deleteOriginItemByItemId(itemId: OriginId, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		const index = this.findOriginItemIndex(itemId.toString());

		return this.deleteOriginItemByIndex(index, structureId);
	}

	private deleteOriginItemByIndex(index: number, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		if (index > -1) {
			const removedElements: Array<OriginItemEntity> = this.origin.splice(index, 1);

			if (removedElements.length > 0) {
				for (let element of removedElements) {
					this.formationManager.unselectRow(element.getId().toString());
				}
				this.formationManager.calculateAllSelected(removedElements.map((f) => f.getId().toString()));
				this.formationManager.calculateAllUnselected();
			}

			return [
				this.createOriginChangedEvent(structureId)
			];
		}

		return [];
	}

	private deleteManyOriginItemByIndex(indexes: Array<number>, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		let events: Array<AggregateEvent<StructureId>> = [];

		indexes.forEach((index: number) => {
			events.push(...this.deleteOriginItemByIndex(index, structureId));
		});

		return events;
	}

	private deleteManyOriginItemByItemID(itemIds: Array<OriginId>, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		let events: Array<AggregateEvent<StructureId>> = [];

		itemIds.forEach((itemId: OriginId) => {
			events.push(...this.deleteOriginItemByItemId(itemId, structureId));
		});

		return events;
	}

	addOriginItem(item: any, structureId: StructureId): ReadonlyArray<AggregateEvent<StructureId>> {
		this.origin.push(item);
		return [this.createOriginChangedEvent(structureId)];
	}

	selectAll(): void {
		this.formationManager.selectAll(this.getEntities().map(e => e.getId().toString()));
	}

	unselectAll(): void {
		this.formationManager.unselectAll();
	}

	toggleRow(itemId: string, type: RowSelectToggleType): void {
		this.formationManager.toggleRow(itemId, type, this.getEntities().map(e => e.getId().toString()));
	}

	getFormation(): FormationManager {
		return this.formationManager;
	}

	private convertItems(items: Array<any>): Array<OriginItemEntity> {
		return items.map((item: any, index: number) => {
			return new OriginItemEntity(new OriginId(RandomStringGenerator.generate()), item, index);
		});
	}

	private createOriginChangedEvent(structureId: StructureId): AggregateEvent<StructureId> {
		return new StructureOriginChangedAggregateEvent(structureId, this.origin);
	}

	private findOriginItem(itemId: string): OriginItemEntity {
		return this.origin.find((item: OriginItemEntity) => item.getId().toString() === itemId);
	}

	private findOriginItemIndex(itemId: string): number {
		return this.origin.findIndex((item: OriginItemEntity) => item.getId().toString() === itemId);
	}

	private recalculatePositions(): void {
		this.entities.forEach((ent: OriginItemEntity, index: number) => {
			ent.setPosition(index);
		});
	}

}
