import { Group } from './group';

export class GroupCollection {

	private readonly groups: Array<Group>;

	private readonly showGroups: boolean;

	constructor(groups: Array<Group>,
				showGroups: boolean) {
		this.groups = groups;
		this.showGroups = showGroups;
	}

	getGroups(): Array<Group> {
		return this.groups;
	}

	isVisible(): boolean {
		return this.showGroups;
	}

}
