import { ClassModifierApi } from './class-modifier-api';
import { Modifier } from '../core/modifier';


export class ClassModifier extends Modifier<ClassModifierApi> {

	constructor(
		private readonly htmlElement?: HTMLElement
	) {
		super(htmlElement);
	}

	protected createModifier(htmlElement: HTMLElement): ClassModifierApi {
		return new ClassModifier.ClassModifier(htmlElement);
	}

	private static readonly ClassModifier = class implements ClassModifierApi {

		private static readonly CLASS = 'class';

		constructor(
			private readonly htmlElement: HTMLElement
		) {
		}

		add(...classes: Array<string>): void {
			this.addClassToDomElement(this.htmlElement, classes);
		}

		remove(...classes: Array<string>): void {
			this.removeClassFromDomElement(this.htmlElement, classes);
		}

		clear(): void {
			this.htmlElement.removeAttribute(ClassModifier.ClassModifier.CLASS);
		}

		private addClassToDomElement(htmlElement: HTMLElement, classes: Array<string>): void {

			for (let i = 0; i < classes.length; i++) {
				htmlElement.classList.add(classes[i]);
			}
		}

		private removeClassFromDomElement(htmlElement: HTMLElement, classes: Array<string>): void {

			for (let i = 0; i < classes.length; i++) {
				htmlElement.classList.remove(classes[i]);
			}
		}

	};

}
