import { SchemaCommandInvoker } from '../../../../../schema/core/api/schema.command-invoker';
import { SchemaReadModelRootId } from '../../../../../schema/core/domain-read/schema.read-model-root-id';
import { schemaGlobalId } from '../../../../../schema/core/domain-read/schema.global-id';
import { StructureId } from '../../../../../structure/core/api/structure.id';
import { structureGlobalId } from '../../../../../structure/core/api/structure.global-id';
import { RowColoring } from '../../../../../schema/core/api/row-coloring';
import { GuiRowColoring, GuiTheme } from '../../../core/api/gui.grid.public-api';
import { GuiGridThemeConverter } from './gui.grid.theme.converter';
import { GuiGridRowColoringConverter } from './coloring/gui.grid.row-coloring.converter';

export class GuiGridThemeCommandInvoker {

	constructor(private readonly schemaCommandInvoker: SchemaCommandInvoker,
				private readonly gridThemeConverter: GuiGridThemeConverter,
				private readonly gridRowColoringConverter: GuiGridRowColoringConverter) {
	}

	setTheme(theme: string | GuiTheme, schemaId: SchemaReadModelRootId = schemaGlobalId, structureId: StructureId = structureGlobalId): void {
		const schemaTheme = this.gridThemeConverter.convert(theme);

		this.schemaCommandInvoker.setTheme(schemaTheme, schemaId, structureId);
	}

	setRowColoring(rowColoring: string | GuiRowColoring, schemaId: SchemaReadModelRootId = schemaGlobalId): void {

		const schemaRowColoring: RowColoring = this.gridRowColoringConverter.convert(rowColoring);

		this.schemaCommandInvoker.setRowColoring(schemaRowColoring, schemaId);
	}

	setVerticalGrid(verticalGrid: boolean, schemaId: SchemaReadModelRootId = schemaGlobalId): void {
		this.schemaCommandInvoker.setVerticalGrid(verticalGrid, schemaId);
	}

	setHorizontalGrid(horizontalGrid: boolean, schemaId: SchemaReadModelRootId = schemaGlobalId): void {
		this.schemaCommandInvoker.setHorizontalGrid(horizontalGrid, schemaId);
	}

}
