import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { UnselectAllUniqueFilterCommand } from './unselect-all-unique-filter.command';
import { StructureAggregate } from '../../../../../core/domain/structure.aggregate';
import { FieldId } from '../../../../../field/core/domain/field/field.id';


@Injectable()
export class UnselectAllUniqueFilterCommandHandler implements CommandHandler<StructureAggregate, UnselectAllUniqueFilterCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<UnselectAllUniqueFilterCommand> {
		return UnselectAllUniqueFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: UnselectAllUniqueFilterCommand): void {
		this.domainEventPublisher.publishFromAggregate(aggregate);
	}

	handleAggregate(structure: StructureAggregate, command: UnselectAllUniqueFilterCommand): void {

		const fieldId: FieldId = command.getFieldId();

		structure.unselectAllUniqueFilter(fieldId);
	}

}
