import { Directive, Input, OnChanges } from '@angular/core';

import { RowDetailConfig } from '../../../row-detail/row-detail.config';
import { Gate } from '../../../../../../common/cdk/component/gate';
import { StructureRowDetailConfigArchive } from '../../../row-detail/structure.row-detail.config-archive';
import { NgChanges } from '../../../../../../common/cdk/component/ng-changes';

@Directive({
	selector: 'gui-structure[rowDetail]'
})
export class StructureRowDetailGate extends Gate implements OnChanges {

	@Input()
	rowDetail: RowDetailConfig;

	constructor(private readonly structureDetailViewConfigArchive: StructureRowDetailConfigArchive) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureRowDetailGate>) {

		if (this.isDefined('rowDetail', changes)) {
			this.structureDetailViewConfigArchive.next(this.rowDetail);
		}
	}

}
