import { ColumnMenuConfig } from '../../../../core/api/column-menu-config';

export class StructureColumnMenuConfig {

	static default(): StructureColumnMenuConfig {
		return new StructureColumnMenuConfig();
	}

	static fromConfig(config: ColumnMenuConfig): StructureColumnMenuConfig {
		return new StructureColumnMenuConfig(
			config.enabled,
			config.sort,
			config.filter,
			config.columnsManager
		);
	}

	private mainMenu: string = 'Menu';

	private filterMenu: string = 'Filter';

	private columnsMenu: string = 'Columns';

	constructor(private enabled: boolean = false,
				private sort: boolean = true,
				private filter: boolean = false,
				private columnsManager: boolean = false) {
	}

	isEnabled(): boolean {
		return this.enabled &&
			(this.sort || this.filter || this.columnsManager);
	}

	isMainEnabled(): boolean {
		return this.sort;
	}

	isSortingEnabled(): boolean {
		return this.sort;
	}

	isFilteringEnabled(): boolean {
		return this.filter;
	}

	isColumnManagerEnabled(): boolean {
		return this.columnsManager;
	}

	getMenus(): Array<string> {

		const menus = [];

		if (this.isMainEnabled()) {
			menus.push(this.getMainMenu());
		}

		if (this.isFilteringEnabled()) {
			menus.push(this.getFilterMenu());
		}

		if (this.isColumnManagerEnabled()) {
			menus.push(this.getColumnMenu());
		}

		return menus;
	}

	getActiveMenu(): string {
		return this.getMenus()[0];
	}

	getMainMenu(): string {
		return this.mainMenu;
	}

	getFilterMenu(): string {
		return this.filterMenu;
	}

	getColumnMenu(): string {
		return this.columnsMenu;
	}

	setMainMenu(mainMenu: string): void {
		this.mainMenu = mainMenu;
	}

	setFilterMenu(filterMenu: string): void {
		this.filterMenu = filterMenu;
	}

	setColumnsMenu(columnsMenu: string): void {
		this.columnsMenu = columnsMenu;
	}

}
