import { UniqueValueId } from './unique-value-id';

export class UniqueValue {

	private readonly id: UniqueValueId;

	private value: any;

	private displayValue: string;

	private enabled: boolean;

	constructor(
		id: UniqueValueId,
		value: any,
		displayValue: string,
		enabled: boolean
	) {
		this.id = id;
		this.value = value;
		this.displayValue = displayValue;
		this.enabled = enabled;
	}

	getId(): UniqueValueId {
		return this.id;
	}

	getValue(): any {
		return this.value;
	}

	getDisplayValue(): string {
		return this.displayValue;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	isDisabled(): boolean {
		return !this.enabled;
	}

	select(): void {
		this.enabled = true;
	}

	unselect(): void {
		this.enabled = false;
	}
}
