import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SchemaThemeRepository } from './schema.theme.repository';
import { SchemaId } from '../../api/schema.id';
import { SchemaThemeSetEvent } from '../../domain/theme/schema-theme-set.event';


@Injectable()
export class SchemaThemeSetEventHandler implements DomainEventHandler<SchemaId, SchemaThemeSetEvent> {

	constructor(private themeRepository: SchemaThemeRepository) {
	}

	forEvent(): DomainEventType<SchemaThemeSetEvent> {
		return SchemaThemeSetEvent;
	}

	handle(event: SchemaThemeSetEvent): void {

		if (event.ofMessageType('SchemaThemeSetEvent')) {
			this.themeRepository.next(event.getAggregateId(), event.getTheme());
		}

	}

}
