import { EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';

import { Gate } from '../../../../common/cdk/component/gate';
import { StructureId } from '../../../core/api/structure.id';
import { SearchConfig } from '../../core/api/search-config';
import { SearchEventRepository } from '../../core/api/search.event-repository';
import { SearchCommandInvoker } from '../../core/api/search.command-invoker';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';


export abstract class SearchingGate extends Gate implements OnChanges, OnInit {

	@Input()
	searching: boolean | SearchConfig;

	@Output()
	searchPhraseChanged: EventEmitter<string> = new EventEmitter();

	protected constructor(protected readonly structureId: StructureId,
						  protected readonly searchEventRepository: SearchEventRepository,
						  protected readonly searchCommandInvoker: SearchCommandInvoker) {
		super();
	}

	ngOnChanges(changes: NgChanges<SearchingGate>) {

		if (this.isDefined('searching', changes)) {

			let searching: SearchConfig;

			if (typeof this.searching === 'boolean') {
				searching = {
					enabled: this.searching
				};
			} else {
				searching = this.searching;
			}

			this.searchCommandInvoker.setSearchingConfig(searching, this.structureId);
		}
	}

	ngOnInit() {

		this.searchEventRepository
			.onSearchPhrase(this.structureId.toReadModelRootId())
			.pipe(this.takeUntil())
			.subscribe((phrase: string) => {
				this.searchPhraseChanged.emit(phrase);
			});
	}

}
