import { TranslationChange } from '../api/translation-change';
import { Translation } from '../api/translation';

export class Dictionary {

	private resolver: (key: string, value: string) => string = (key: string, value: string) => value;

	private readonly translation: Map<string, string> = new Map<string, string>();

	changeTranslation(change: TranslationChange | Translation): void {
		for (const key of Object.keys(change)) {
			this.translation.set(key, change[key]);
		}
	}

	getTranslation(): Translation {
		return Array.from(this.translation)
					.reduce((obj, [key, value]) => (
						Object.assign(obj, { [key]: this.resolver(key, value) })
					), {}) as Translation;
	}

	setResolver(resolver: (key: string, value: string) => string): void {
		this.resolver = resolver;
	}

}
