import { Injectable } from '@angular/core';

import { FieldConfig } from '../../api/field.config';
import { Field } from './field';
import { DataFieldFactory } from './data/data-field.factory';
import { FieldIdGenerator } from './field-id.generator';


@Injectable()
export class FieldFactory {

	constructor(private readonly fieldIdGenerator: FieldIdGenerator,
				private readonly dataFieldFactory: DataFieldFactory) {
	}

	create(configs: Array<FieldConfig>): Array<Field> {

		if (!configs) {
			return [];
		}

		return configs.map((fieldConfig: FieldConfig, index: number) => {

			const fieldId = this.fieldIdGenerator.generateId(),
				dataField = this.dataFieldFactory.create(fieldConfig);

			return new Field(fieldId, dataField, this.getFieldName(fieldConfig, index));
		});
	}

	private getFieldName(fieldConfig: FieldConfig, index: number): string {

		if (typeof fieldConfig.field === 'string') {
			return fieldConfig.field.toLowerCase();
		} else {
			return 'Field #' + index;
		}
	}

}
