import { StructureColumnMenuConfigArchive } from './header/menu/config/structure.column-menu-config.archive';
import { PagingDisplayModeArchive } from '../../paging/feature/mode/paging-display-mode.archive';
import { RowSelectEnabledRepository } from '../../source/core/api/formation/set-enabled/row-select-enabled.repository';
import { SearchEventRepository } from '../../search/core/api/search.event-repository';
import { StructureCellEditCloseAllService } from './edit/structure.cell-edit-close-all.service';
import { SchemaReadModelRootId } from '../../../schema/core/domain-read/schema.read-model-root-id';
import { SchemaCommandInvoker } from '../../../schema/core/api/schema.command-invoker';
import { StructureHeaderBottomEnabledArchive } from './header/structure-header-bottom-enabled.archive';
import { StructureHeaderTopEnabledArchive } from './header/structure-header-top-enabled.archive';
import { SchemaEventRepository } from '../../../schema/core/api/schema.event-repository';
import { StructureRowDetailConfigArchive } from './row-detail/structure.row-detail.config-archive';
import { StructureRowDetailService } from './row-detail/structure.row-detail.service';
import { PagingFeatureModule } from '../../paging/feature/paging.feature-module';
import { StructureTitlePanelConfigArchive } from './panel/banner-panels/title-panel/structure.title-panel.config-archive';
import { StructureFooterPanelConfigArchive } from './panel/banner-panels/footer-panel/structure.footer-panel.config-archive';
import { StructureInfoPanelConfigService } from './panel/info/structure.info-panel-config.service';
import { SortingFeatureModule } from '../../sorting/feature/sorting.feature-module';
import { SortingCommandInvoker } from '../../sorting/core/api/sorting.command-invoker';
import { SearchFeatureModule } from '../../search/feature/search.feature-module';
import { FieldFeatureModule } from '../../field/feature/field.feature-module';
import { SearchCommandInvoker } from '../../search/core/api/search.command-invoker';
import { FieldCommandInvoker } from '../../field/core/api/field.command-invoker';
import { CommandDispatcher, DomainEventBus } from '@generic-ui/hermes';
import { ColumnFieldFactory } from '../../../composition/core/domain/column/field/colum-field.factory';
import { FilterContainerRef } from '../../filter/core/api/config/filter-container-ref';
import { filterContainerToken } from '../../filter/core/api/config/filter-container-token';
import { FormationCommandInvoker } from '../../source/core/api/formation/formation.command-invoker';
import { RowSelectionTypeArchive } from '../../source/core/api/formation/type/row-selection-type.archive';
import { SourceFeatureModule } from '../../source/feature/source.feature-module';
import { TranslationService } from '../../../l10n/core/api/translation.service';
import { TranslationFeatureModule } from '../../../l10n/feature/translation.feature-module';
import { SchemaRowClassArchive } from '../../../schema/core/api/styling/schema.row-class.archive';
import { SchemaRowStyleArchive } from '../../../schema/core/api/styling/schema.row-style.archive';
import { SchemaFeatureModule } from '../../../schema/feature/schema.feature-module';
import { StreamCloser } from '../../../common/cdk/reactive/stream-closer';
import { StyleModifier } from '../../../common/cdk/dom/style/style-modifier';
import { NgChanges } from '../../../common/cdk/component/ng-changes';


/** @internal */
export function structureIdFactory(generator: StructureIdGenerator): StructureId {
	return new StructureId('gui-' + generator.generateId());
}

/** @internal */
export function compositionIdFactory(generator: StructureIdGenerator): CompositionId {
	return new CompositionId('gui-' + generator.generateId());
}

/** @internal */
export function schemaIdFactory(generator: StructureIdGenerator): SchemaReadModelRootId {
	return new SchemaReadModelRootId('gui-' + generator.generateId());
}

/** @internal */
export const structureComponentSelfProviders = [
	{
		provide: StructureId,
		useFactory: structureIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	{
		provide: CompositionId,
		useFactory: compositionIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	{
		provide: SchemaReadModelRootId,
		useFactory: schemaIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	...localProviders,

	SchemaCssClassManager,
	StructureCellEditArchive,
	StructureCellEditCloseAllService,
	StructureCellEditStore,
	StructureEditModeArchive,
	StructureInfoPanelArchive,
	StructureInfoPanelConfigService,
	StructureSummariesArchive,
	StructureSummariesConfigService,
	StructureColumnMenuConfigArchive,

	PagingFeatureModule.forComponent(),
	SortingFeatureModule.forComponent(),
	FieldFeatureModule.forComponent(),
	SearchFeatureModule.forComponent(),
	SourceFeatureModule.forComponent(),
	SchemaFeatureModule.forComponent(),
	TranslationFeatureModule.forComponent(),

	RowSelectEnabledRepository,
	StructureHeaderTopEnabledArchive,
	StructureHeaderBottomEnabledArchive,
	StructureRowDetailConfigArchive,
	StructureRowDetailService,
	StructureTitlePanelConfigArchive,
	StructureFooterPanelConfigArchive
];

/** @internal */
@Component({
	selector: 'gui-structure',
	templateUrl: './structure.component.html',
	styleUrls: [
		'./style/styles.ngx.scss',
		'./style/structure.ngx.scss',
		'./style/structure-column-manager.ngx.scss',
		'./style/structure-summaries.ngx.scss',
		'./style/structure-info-modal.ngx.scss',
		'./style/structure-responsive.ngx.scss',
		'./style/structure-flex.ngx.scss',
		'./style/structure-header-menu.ngx.scss',
		'../../../composition/feature/style/composition.ngx.scss',
		'./style/themes/fabric.structure.ngx.scss',
		'./style/themes/material.structure.ngx.scss',
		'./style/themes/dark.structure.ngx.scss',
		'./style/themes/light.structure.ngx.scss',
		'./style/themes/generic.structure.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-structure-border]': 'isBorderEnabled()',
		'[id]': 'structureId.toString()'
	},
	providers: [
		...structureComponentSelfProviders,
		{
			provide: structureComponentToken,
			useExisting: StructureComponent
		},
		{
			provide: filterContainerToken,
			useExisting: StructureComponent
		}
	]
})
export class StructureComponent extends StructureGateway implements OnChanges, OnInit, AfterViewInit, OnDestroy, FilterContainerRef {

	loaderEnabled: boolean = false;

	circleLoaderEnabled: boolean = true;

	initialLoaderAnimation: boolean = false;

	private structure: StructureReadModelRoot;

	private readonly localStreamCloser = new StreamCloser();

	private readonly styleModifier: StyleModifier;

	constructor(structureId: StructureId,
				compositionId: CompositionId,
				pagingCommandService: PagingCommandInvoker,
				pagingEventRepository: PagingEventRepository,
				sourceCommandDispatcher: SourceCommandInvoker,
				sourceEventService: SourceEventService,
				sortingCommandDispatcher: SortingCommandInvoker,
				searchCommandDispatcher: SearchCommandInvoker,
				fieldCommandDispatcher: FieldCommandInvoker,
				schemaCommandDispatcher: SchemaCommandInvoker,
				compositionCommandDispatcher: CompositionCommandInvoker,
				compositionEventRepository: CompositionEventRepository,
				formationEventService: FormationEventRepository,
				structureCommandService: StructureCommandDispatcher,
				structureEditModeArchive: StructureEditModeArchive,
				structureCellEditArchive: StructureCellEditArchive,
				structureInfoPanelArchive: StructureInfoPanelArchive,
				structureInfoPanelConfigService: StructureInfoPanelConfigService,
				structureSummariesConfigService: StructureSummariesConfigService,
				structureCellEditStore: StructureCellEditStore,
				columnFieldFactory: ColumnFieldFactory,
				structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				pagingDisplayModeArchive: PagingDisplayModeArchive,
				rowSelectEnabledArchive: RowSelectEnabledRepository,
				rowSelectionTypeArchive: RowSelectionTypeArchive,
				schemaRowClassArchive: SchemaRowClassArchive,
				schemaRowStyleArchive: SchemaRowStyleArchive,
				formationCommandDispatcher: FormationCommandInvoker,
				searchEventRepository: SearchEventRepository,
				structureHeaderTopEnabledArchive: StructureHeaderTopEnabledArchive,
				structureHeaderBottomEnabledArchive: StructureHeaderBottomEnabledArchive,
				structureDetailViewConfigArchive: StructureRowDetailConfigArchive,
				structureTitlePanelConfigArchive: StructureTitlePanelConfigArchive,
				structureFooterPanelConfigArchive: StructureFooterPanelConfigArchive,
				schemaEventRepository: SchemaEventRepository,
				translationService: TranslationService,
				private readonly elementRef: ElementRef,
				private readonly detectorRef: ChangeDetectorRef,
				public readonly injector: Injector,
				private readonly structureDefinition: StructureDefinition,
				private readonly structureReadModelService: StructureWarehouse,
				private readonly compositionReadModelService: CompositionWarehouse,
				private readonly schemaStylesManager: SchemaCssClassManager,
				private readonly schemaReadModelRootId: SchemaReadModelRootId,
				domainEventBus: DomainEventBus,
				commandDispatcher: CommandDispatcher,
				private readonly structureDetailViewService: StructureRowDetailService) {

		super(
			detectorRef,
			elementRef,
			domainEventBus,
			commandDispatcher,
			structureId,
			compositionId,
			schemaReadModelRootId,
			structureCommandService,
			pagingCommandService,
			pagingEventRepository,
			sortingCommandDispatcher,
			searchCommandDispatcher,
			fieldCommandDispatcher,
			sourceCommandDispatcher,
			sourceEventService,
			schemaCommandDispatcher,
			compositionCommandDispatcher,
			compositionEventRepository,
			formationEventService,
			structureEditModeArchive,
			structureCellEditArchive,
			structureInfoPanelArchive,
			structureInfoPanelConfigService,
			structureSummariesConfigService,
			structureCellEditStore,
			columnFieldFactory,
			structureColumnMenuConfigArchive,
			pagingDisplayModeArchive,
			rowSelectEnabledArchive,
			rowSelectionTypeArchive,
			schemaRowClassArchive,
			schemaRowStyleArchive,
			formationCommandDispatcher,
			searchEventRepository,
			structureHeaderTopEnabledArchive,
			structureHeaderBottomEnabledArchive,
			structureDetailViewConfigArchive,
			structureTitlePanelConfigArchive,
			structureFooterPanelConfigArchive,
			schemaEventRepository,
			translationService
		);
		this.styleModifier = new StyleModifier(this.elementRef.nativeElement);
		structureCommandService.createStructure();
		compositionCommandDispatcher.createComposition();
		schemaCommandDispatcher.create();
	}

	ngOnChanges(changes: NgChanges<StructureComponent>) {
		super.ngOnChanges(changes);
	}

	ngOnInit() {
		super.ngOnInit();

		this.structureReadModelService
			.onStructure()
			.pipe(
				this.localStreamCloser.takeUntil()
			)
			.subscribe((structureReadModelRoot: StructureReadModelRoot) => {
				this.structure = structureReadModelRoot;

				this.loaderEnabled = structureReadModelRoot.getSource().isLoading();

				this.circleLoaderEnabled = structureReadModelRoot.isLoaderVisible();

				if (this.loaderEnabled && !this.initialLoaderAnimation) {
					this.initialLoaderAnimation = true;
				}

				this.detectorRef.detectChanges();
			});

		this.structureDetailViewService.init(this.elementRef);
	}

	ngAfterViewInit() {
		let width = this.elementRef.nativeElement.offsetWidth;

		if (width > 0) {
			this.compositionCommandDispatcher.setContainerWidth(width);
		} else {

			/**
			 * When gui-grid is in dynamic container which is created later then grid.
			 */
			timer(0)
				.pipe(
					this.localStreamCloser.takeUntil()
				)
				.subscribe(() => {

					width = this.elementRef.nativeElement.offsetWidth;

					if (width > 0) {
						this.compositionCommandDispatcher.setContainerWidth(width);
					}
				});
		}

		this.schemaStylesManager
			.init(this.elementRef, this.schemaReadModelRootId);

		this.compositionReadModelService
			.onWidth()
			.pipe(
				this.localStreamCloser.takeUntil()
			)
			.subscribe((width: number) => {
				this.styleModifier.getHost().setWidth(width);
			});
	}

	ngOnDestroy() {
		this.localStreamCloser.unsubscribe();
	}

	isBorderEnabled(): boolean {
		return this.structureDefinition.isBorderEnabled();
	}

	getStructureId(): StructureId {
		return this.structureId;
	}

	getElementRef(): ElementRef {
		return this.elementRef;
	}

	protected getSelectorName(): string {
		return 'gui-structure';
	}

}
