import { SchemaId } from '../../api/schema.id';
import { SchemaTheme } from '../../api/schema-theme';
import { SchemaCommand } from '../schema.command';


export class SetSchemaThemeCommand extends SchemaCommand {

	constructor(schemaId: SchemaId,
				private readonly theme: SchemaTheme) {
		super(schemaId, 'SetSchemaThemeCommand');
	}

	getTheme(): SchemaTheme {
		return this.theme;
	}

}
