import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { UnselectUniqueFilterCommand } from './unselect-unique-filter.command';
import { StructureAggregate } from '../../../../../core/domain/structure.aggregate';
import { FieldId } from '../../../../../field/core/domain/field/field.id';


@Injectable()
export class UnselectUniqueFilterCommandHandler implements CommandHandler<StructureAggregate, UnselectUniqueFilterCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<UnselectUniqueFilterCommand> {
		return UnselectUniqueFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: UnselectUniqueFilterCommand): void {
		this.domainEventPublisher.publishFromAggregate(aggregate);
	}

	handleAggregate(structure: StructureAggregate, command: UnselectUniqueFilterCommand): void {

		const fieldId: FieldId = command.getFieldId(),
			uniqueValueId = command.getUniqueValueId();

		structure.unselectUniqueFilter(fieldId, uniqueValueId);
	}

}
