import { GuiRowColoring, GuiSelectedRow, GuiTheme } from '../../../core/api/gui.grid.public-api';
import { FormationCommandInvoker } from '../../../../../structure/source/core/api/formation/formation.command-invoker';
import { StructureId } from '../../../../../structure/core/api/structure.id';
import { SourceCommandInvoker } from '../../../../../structure/source/core/api/source.command-invoker';
import { SelectedRow } from '../../../../../structure/source/core/api/formation/selected-row';
import { FormationWarehouse } from '../../../../../structure/source/core/api/formation/formation.warehouse';
import { SearchCommandInvoker } from '../../../../../structure/search/core/api/search.command-invoker';
import { SchemaReadModelRootId } from '../../../../../schema/core/domain-read/schema.read-model-root-id';
import { GuiGridThemeCommandInvoker } from '../theme/gui.grid.theme.command-invoker';
import { StructureCommandDispatcher } from '../../../../../structure/core/api/structure.command-dispatcher';


export interface GuiGridApi {

	setSource(source: Array<any>): void

	showLoading(): void;

	hideLoading(): void;

	getSelectedRows(): Array<GuiSelectedRow>;

	selectAll(): void;

	unselectAll(): void;

	deleteSelectedRows(): void;

	deleteRow(row: GuiSelectedRow): void;

	deleteRows(rows: Array<GuiSelectedRow>): void;

	// Searching
	search(phrase: string): void;

	clearSearchPhrase(): void;

	// View port
	scrollToTop(): void;

	scrollToBottom(): void;

	scrollToRowByIndex(index: number): void;

	// Visual
	setTheme(theme: string | GuiTheme): void;

	setVerticalGrid(enabled: boolean): void;

	setHorizontalGrid(enabled: boolean): void;

	setRowColoring(rowColoring: string | GuiRowColoring): void;

}


export class GuiGridApiProvider {

	private readonly structureId: StructureId;

	private readonly schemaId: SchemaReadModelRootId;

	private readonly formationCommandInvoker: FormationCommandInvoker;

	private readonly formationWarehouse: FormationWarehouse;

	private readonly sourceCommandInvoker: SourceCommandInvoker;

	private readonly searchCommandInvoker: SearchCommandInvoker;

	private readonly gridThemeCommandInvoker: GuiGridThemeCommandInvoker;

	private readonly structureCommandDispatcher: StructureCommandDispatcher;

	constructor(structureId: StructureId,
				schemaId: SchemaReadModelRootId,
				formationCommandDispatcher: FormationCommandInvoker,
				formationWarehouse: FormationWarehouse,
				sourceCommandInvoker: SourceCommandInvoker,
				searchCommandInvoker: SearchCommandInvoker,
				gridThemeCommandInvoker: GuiGridThemeCommandInvoker,
				structureCommandDispatcher: StructureCommandDispatcher) {
		this.structureId = structureId;
		this.schemaId = schemaId;
		this.formationCommandInvoker = formationCommandDispatcher;
		this.formationWarehouse = formationWarehouse;
		this.sourceCommandInvoker = sourceCommandInvoker;
		this.searchCommandInvoker = searchCommandInvoker;
		this.gridThemeCommandInvoker = gridThemeCommandInvoker;
		this.structureCommandDispatcher = structureCommandDispatcher;
	}

	provide(): GuiGridApi {

		const structureId = this.structureId,
			schemaId = this.schemaId,
			fcd = this.formationCommandInvoker,
			fw = this.formationWarehouse,
			scd = this.sourceCommandInvoker,
			searchCommandInvoker = this.searchCommandInvoker,
			gridThemeCommandInvoker = this.gridThemeCommandInvoker,
			structureCommandDispatcher = this.structureCommandDispatcher;

		return {

			setSource(source: Array<any>): void {
				scd.setOrigin(source, structureId);
			},

			showLoading(): void {
				scd.setLoading(true, structureId);
			},

			hideLoading(): void {
				scd.setLoading(false, structureId);
			},

			deleteRow(row: GuiSelectedRow): void {
				scd.deleteRow(new SelectedRow(row.data, row.index, row.itemId), structureId);
			},

			deleteRows(rows: Array<GuiSelectedRow>): void {
				const selectedRows = rows.map((row: GuiSelectedRow) => new SelectedRow(row.data, row.index, row.itemId));

				scd.deleteRows(selectedRows, structureId);
			},

			deleteSelectedRows(): void {
				// this.sourceCommandDispatcher.
			},

			getSelectedRows(): Array<GuiSelectedRow> {

				const rows = fw.getSelectedRows(structureId);

				return rows.map((r: SelectedRow) => {
					return { data: r.getData(), index: r.getIndex(), itemId: r.getItemId() };
				});
			},

			selectAll(): void {
				fcd.selectAll(structureId);
			},

			unselectAll(): void {
				fcd.unselectAll(structureId);
			},

			search(phrase: string): void {
				searchCommandInvoker.search(phrase, structureId);
			},

			clearSearchPhrase(): void {
				searchCommandInvoker.search('', structureId);
			},

			// View port
			scrollToTop(): void {
				structureCommandDispatcher.scrollToTop(structureId);
			},

			scrollToBottom(): void {
				structureCommandDispatcher.scrollToBottom(structureId);
			},

			scrollToRowByIndex(index: number): void {
				structureCommandDispatcher.scrollToIndex(index, structureId);
			},

			setTheme(theme: string | GuiTheme): void {
				gridThemeCommandInvoker.setTheme(theme, schemaId, structureId);
			},

			setVerticalGrid(enabled: boolean): void {
				gridThemeCommandInvoker.setVerticalGrid(enabled, schemaId);
			},

			setHorizontalGrid(enabled: boolean): void {
				gridThemeCommandInvoker.setHorizontalGrid(enabled, schemaId);
			},

			setRowColoring(rowColoring: string | GuiRowColoring): void {
				gridThemeCommandInvoker.setRowColoring(rowColoring, schemaId);
			}

		};

	}


}
