import { Directive, Input, OnChanges } from '@angular/core';
import { Gate } from '../../../../../../common/cdk/component/gate';
import { GuiRowStyle } from '../../../../../../gui/grid/core/api/gui.grid.public-api';
import { SchemaRowStyle } from '../../../../../../schema/core/api/styling/schema.row-style';
import { SchemaRowStyleArchive } from '../../../../../../schema/core/api/styling/schema.row-style.archive';
import { NgChanges } from '../../../../../../common/cdk/component/ng-changes';

@Directive({
	selector: 'gui-structure[rowStyle]'
})
export class StructureRowStyleGate extends Gate implements OnChanges {

	@Input()
	rowStyle: GuiRowStyle;

	constructor(private readonly schemaRowStyleArchive: SchemaRowStyleArchive) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureRowStyleGate>) {

		if (this.isDefined('rowStyle', changes)) {
			this.schemaRowStyleArchive.next(this.rowStyle as SchemaRowStyle);
		}
	}

}
