import { Directive, Input, OnChanges } from '@angular/core';

import { InfoPanelConfig } from '../../../panel/info/info-panel.config';
import { Gate } from '../../../../../../common/cdk/component/gate';
import { StructureInfoPanelConfigService } from '../../../panel/info/structure.info-panel-config.service';
import { NgChanges } from '../../../../../../common/cdk/component/ng-changes';


@Directive({
	selector: 'gui-structure[infoPanel]'
})
export class StructureInfoPanelGate extends Gate implements OnChanges {

	@Input()
	infoPanel: boolean | InfoPanelConfig;

	constructor(protected readonly structureInfoPanelConfigService: StructureInfoPanelConfigService) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureInfoPanelGate>) {

		if (this.isDefined('infoPanel', changes)) {
			if (typeof this.infoPanel === 'boolean') {
				this.infoPanel = {
					enabled: this.infoPanel
				};
			}

			this.structureInfoPanelConfigService.set(this.infoPanel);
		}

	}

}
