import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { InitFieldsCommand } from './init-fields.command';
import { StructureAggregate } from '../../../../core/domain/structure.aggregate';
import { FieldConfig } from '../../api/field.config';


@Injectable()
export class InitFieldsCommandHandler implements CommandHandler<StructureAggregate, InitFieldsCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<InitFieldsCommand> {
		return InitFieldsCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: InitFieldsCommand): void {
		this.domainEventPublisher.publishFromAggregate(aggregate);
	}

	handleAggregate(structure: StructureAggregate, command: InitFieldsCommand): void {

		const fieldConfigs: Array<FieldConfig> = command.getFieldConfigs();

		structure.createFields(fieldConfigs);
	}


}
