import { Component, Input } from '@angular/core';

import { CellTemplateWithContext } from '../../../../../../../composition/core/domain-read/definition/cell-template-with-context';
import { CompositionCommandInvoker } from '../../../../../../../composition/core/api/composition.command-invoker';
import { StructureColumnConfigService } from '../../structure.column-config.service';


@Component({
	selector: 'gui-structure-column-config-column-hide',
	template: `
		<div class="gui-header-menu-item"
			 (click)="hideColumn(column)">
			{{'headerMenuMainTabHideColumn' | guiTranslate}}
		</div>
	`
})
export class StructureColumnConfigColumnHideComponent {

	@Input()
	column: CellTemplateWithContext;

	constructor(private compositionCommandService: CompositionCommandInvoker,
				private structureColumnConfigService: StructureColumnConfigService) {
	}

	hideColumn(column: CellTemplateWithContext): void {
		this.compositionCommandService.disableColumn(column.getColumnDefinitionId());

		this.structureColumnConfigService.close();
	}

}
