import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../../../../core/domain/structure-aggregate.repository';
import { ToggleFilterCommand } from './toggle-filter.command';
import { FieldId } from '../../../../field/core/domain/field/field.id';
import { FilterToggledEvent } from './filter-toggled.event';
import { StructureAggregate } from '../../../../core/domain/structure.aggregate';


@Injectable()
export class ToggleFilterCommandHandler implements CommandHandler<StructureAggregate, ToggleFilterCommand> {

	constructor(private readonly structureAggregateRepository: StructureAggregateRepository,
				private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<ToggleFilterCommand> {
		return ToggleFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: ToggleFilterCommand): void {
		this.domainEventPublisher.publish(new FilterToggledEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: ToggleFilterCommand): void {

		const fieldId: FieldId = command.getFieldId(),
			externalFieldId: string = command.getExternalFilterId(),
			filterValue = command.getFilterValue();

		aggregate.toggleFilter(fieldId, externalFieldId, filterValue);
	}

}
