import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { AggregateId } from '@generic-ui/hermes';

import { StructureReadModelRepository } from './read/structure.read-model-repository';
import { structureGlobalId } from '../../../core/api/structure.global-id';
import { StructureReadModelRoot } from './read/structure.read-model-root';


@Injectable()
export class StructureWarehouse {

	constructor(private readonly structureRepository: StructureReadModelRepository) {
	}

	onStructure(aggregateId: AggregateId = structureGlobalId): Observable<StructureReadModelRoot> {
		return this.structureRepository.onStructure(aggregateId);
	}

}
