import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { Warehouse } from '@generic-ui/hermes';

import { SearchingRepository } from './enabled/searching.repository';
import { StructureId } from '../../../core/api/structure.id';
import { structureGlobalId } from '../../../core/api/structure.global-id';
import { SearchPhraseRepository } from './phrase/search-phrase.repository';


@Injectable()
export class SearchWarehouse implements Warehouse {

	constructor(private readonly structureSearchingRepository: SearchingRepository,
				private readonly searchPhraseRepository: SearchPhraseRepository) {
	}

	onSearchEnabled(structureId: StructureId = structureGlobalId): Observable<boolean> {
		return this.structureSearchingRepository.onSearchingEnabled(structureId);
	}

	onPhrase(structureId: StructureId = structureGlobalId): Observable<string> {
		return this.searchPhraseRepository.onPhrase(structureId);
	}

}
