import { OriginId } from '../../domain/origin/origin-id';

export class SelectedRow {

	private readonly index: number;

	private readonly itemId: OriginId;

	private readonly data: any;

	constructor(data: any,
				index: number,
				itemId: OriginId) {
		this.data = data;
		this.index = index;
		this.itemId = itemId;
	}

	getData(): any {
		return this.data;
	}

	getIndex(): number {
		return this.index;
	}

	getItemId(): OriginId {
		return this.itemId;
	}

}
