import { Directive, Input, OnChanges } from '@angular/core';

import { Gate } from '../../../../../common/cdk/component/gate';
import { StructureTitlePanelConfigArchive } from '../../panel/banner-panels/title-panel/structure.title-panel.config-archive';
import { StructureFooterPanelConfigArchive } from '../../panel/banner-panels/footer-panel/structure.footer-panel.config-archive';
import { NgChanges } from '../../../../../common/cdk/component/ng-changes';
import { StructureBannerPanelConfig } from '../../panel/banner-panels/structure-banner-panel.config';

@Directive({
	selector: 'gui-structure[titlePanel][footerPanel]'
})
export class StructurePanelGate extends Gate implements OnChanges {

	@Input()
	titlePanel: StructureBannerPanelConfig;

	@Input()
	footerPanel: StructureBannerPanelConfig;

	constructor(private readonly structureTitlePanelConfigArchive: StructureTitlePanelConfigArchive,
				private readonly structureFooterPanelConfigArchive: StructureFooterPanelConfigArchive) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructurePanelGate>) {

		if (this.isDefined('titlePanel', changes)) {
			this.structureTitlePanelConfigArchive.next(this.titlePanel);
		}

		if (this.isDefined('footerPanel', changes)) {
			this.structureFooterPanelConfigArchive.next(this.footerPanel);
		}
	}

}
