import { StructureId } from '../../../../../core/api/structure.id';
import { OriginId } from '../origin-id';
import { StructureCommand } from '../../../../../core/domain/structure.command';

export enum DeleteCommandPayloadType {
	INDEX,
	ITEM_ID,
	MANY_INDEX,
	MANY_ITEM_ID
}

export type DeleteOriginItemPayload = {

	index?: number;

	itemId?: OriginId;

}

export class DeleteOriginItemCommand extends StructureCommand {

	static byIndex(structureId: StructureId, index: number): DeleteOriginItemCommand {
		return new DeleteOriginItemCommand(structureId, index, DeleteCommandPayloadType.INDEX);
	}

	static byManyIndex(structureId: StructureId, indexes: Array<number>): DeleteOriginItemCommand {
		return new DeleteOriginItemCommand(structureId, indexes, DeleteCommandPayloadType.MANY_INDEX);
	}

	static byItemId(structureId: StructureId, itemId: OriginId): DeleteOriginItemCommand {
		return new DeleteOriginItemCommand(structureId, itemId, DeleteCommandPayloadType.ITEM_ID);
	}

	static byManyItemId(structureId: StructureId, itemIds: Array<OriginId>): DeleteOriginItemCommand {
		return new DeleteOriginItemCommand(structureId, itemIds, DeleteCommandPayloadType.MANY_ITEM_ID);
	}

	constructor(structureId: StructureId,
				private readonly payload: number | OriginId | Array<number> | Array<OriginId>,
				private readonly type: DeleteCommandPayloadType) {
		super(structureId, 'DeleteOriginItemCommand');
	}

	getType(): DeleteCommandPayloadType {
		return this.type;
	}

	getPayload(): number | OriginId | Array<number> | Array<OriginId> {
		return this.payload;
	}

}
