import { FieldId } from './field/field.id';
import { Field } from './field/field';
import { FieldConfig } from '../api/field.config';
import { FieldFactory } from './field/field.factory';


export class FieldCollection {

	private readonly fields = new Map<string, Field>();

	constructor(private readonly fieldFactory: FieldFactory) {
	}

	getField(fieldId: FieldId): Field {

		if (fieldId === null) {
			return this.getAllFields()[0];
		}

		return this.fields.get(fieldId.getId());
	}

	getAllFieldIds(): Array<string> {
		return Array.from(this.fields.keys());
	}

	getAllFields(): Array<Field> {
		return Array.from(this.fields.values());
	}

	getFieldsAsMap(): Map<string, Field> {
		return this.fields;
	}

	initFields(fieldConfigs: Array<FieldConfig>): void {

		const fields: Array<Field> = this.fieldFactory.create(fieldConfigs);

		this.addFields(fields);
	}

	addFields(fields: Array<Field>): void {
		for (let field of fields) {
			this.addField(field);
		}
	}

	private addField(field: Field): void {
		this.fields.set(field.getId().toString(), field);
	}
}
