import { ChangeDetectorRef, OnDestroy, Pipe, PipeTransform } from '@angular/core';
import { Subscription } from 'rxjs';

import { TranslationService } from '../core/api/translation.service';
import { Translation } from '../core/api/translation';


@Pipe({
	name: 'guiTranslate',
	pure: false
})
export class TranslationPipe implements PipeTransform, OnDestroy {

	private actualTranslationValue: string = '';

	private subscription: Subscription;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly translationService: TranslationService) {
	}

	ngOnDestroy() {
		if (this.subscription) {
			this.subscription.unsubscribe();
		}
	}

	transform(key: string): string {

		if (this.subscription) {
			this.subscription.unsubscribe();
		}

		this.subscription =
			this.translationService
				.onTranslation()
				.subscribe((translation: Translation) => {
					const value = translation[key];

					if (!value) {
						this.actualTranslationValue = key;
					}

					this.actualTranslationValue = value;
					this.changeDetectorRef.markForCheck();
				});

		return this.actualTranslationValue;
	}

}
