import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, ViewChild, ViewEncapsulation } from '@angular/core';

import { EditCommunicationComponent } from '../edit-communication.component';

import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';

import { StreamCloser } from '../../../../../common/cdk/reactive/stream-closer';


@Component({
	selector: 'gui-date-edit',
	template: `

		<gui-date-picker #datepicker
						 [selectDate]="value"
						 [name]="filterFieldName"
						 [openDialog]="true"
						 [onlyDialog]="false"
						 [parentElement]="parent"
						 (dialogOpened)="dialogOpened($event)"
						 (dateSelected)="toggle($event)">
		</gui-date-picker>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class DateEditTemplateComponent extends EditCommunicationComponent<Date> {

	@ViewChild('datepicker', { read: ElementRef, static: true })
	datePickerRef: ElementRef;

	readonly filterFieldName = 'dateEdit';

	private opened = false;

	private readonly localStreamCloser = new StreamCloser();

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef) {
		super(changeDetectorRef, elementRef);
	}

	ngAfterViewInit() {
		const inputElement = this.datePickerRef.nativeElement.querySelector('.gui-date-picker-input');

		const keyup$ = fromEvent(inputElement, 'keyup');

		keyup$
			.pipe(
				filter((e: any) => e.keyCode === this.ENTER_KEY_CODE),
				this.localStreamCloser.takeUntil()
			)
			.subscribe(() => {
				this.localStreamCloser.unsubscribe();
				this.submit();
			});

		keyup$
			.pipe(
				filter((e: any) => e.keyCode === this.ESC_KEY_CODE),
				this.localStreamCloser.takeUntil()
			)
			.subscribe(() => {
				this.localStreamCloser.unsubscribe();
				this.cancel();
			});
	}

	ngOnDestroy() {
		this.localStreamCloser.unsubscribe();
	}

	toggle(changed: Date): void {
		this.valueChanges.emit(changed);
	}

	dialogOpened(opened: boolean): void {
		this.opened = opened;

		if (!opened) {
			this.localStreamCloser.unsubscribe();
			this.submit();
		}
	}

	protected getSelectorName(): string {
		return 'gui-date-edit';
	}

}
