import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map, switchMap, take } from 'rxjs/operators';

import { DomainEvent, DomainEventBus, EventRepository } from '@generic-ui/hermes';

import { PagingWarehouse } from './paging.warehouse';
import { StructureId } from '../../../core/api/structure.id';
import { NextPageEvent } from '../domain/next-page/next-page.event';
import { PrevPageEvent } from '../domain/prev-page/prev-page.event';
import { PageChangedEvent } from '../domain/page/page-changed.event';
import { Paging } from './read/paging';
import { PagesizeChangedEvent } from '../domain/change-pagesize/pagesize-changed.event';
import { StructureReadModelRootId } from '../../../grid/core/api/read/structure.read-model-root-id';


@Injectable()
export class PagingEventRepository extends EventRepository<StructureReadModelRootId, StructureId> {

	constructor(private readonly eventBus: DomainEventBus,
				private readonly pagingReadModelService: PagingWarehouse) {
		super(eventBus);
	}

	onPageChange(structureId: StructureReadModelRootId): Observable<number> {

		return this.eventBus
				   .ofEvent(
					   PrevPageEvent as any,
					   NextPageEvent as any,
					   PageChangedEvent as any
				   )
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toAggregateId().toString()),
					   switchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingReadModelService
									  .onPaging(structureId.toAggregateId())
									  .pipe(
										  map((paging: Paging) => paging.getPage()),
										  take(1)
									  );
					   })
				   );
	}

	onPageSizeChange(structureId: StructureReadModelRootId): Observable<number> {

		return this.onEvent(structureId, PagesizeChangedEvent)
				   .pipe(
					   switchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingReadModelService
									  .onPaging(structureId.toAggregateId())
									  .pipe(
										  map((paging: Paging) => paging.getPageSize()),
										  take(1)
									  );
					   })
				   );
	}

}
