import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { GuiSelectOption } from '@generic-ui/fabric';
import { SchemaWarehouse } from '../../core/api/schema.warehouse';
import { SchemaRowColoring } from '../../core/api/schema-row-coloring';
import { SchemaTheme } from '../../core/api/schema-theme';
import { SchemaCommandInvoker } from '../../core/api/schema.command-invoker';
import { SchemaReadModelRootId } from '../../core/domain-read/schema.read-model-root-id';
import { RowColoring } from '../../core/api/row-coloring';
import { SmartComponent } from '../../../common/cdk/component/smart-component';


@Component({
	selector: 'div[gui-structure-schema-manager]',
	templateUrl: `./structure.schema-manger.component.html`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class StructureSchemaMangerComponent extends SmartComponent implements OnInit {

	coloring: Array<GuiSelectOption> = Object.keys(SchemaRowColoring)
											 .map((key: string) => SchemaRowColoring[key])
											 .filter((val) => !Number.isInteger(val))
											 .map((value: string) => {
												 return {
													 value: value,
													 name: value
												 };
											 });

	themes: Array<GuiSelectOption> = Object.keys(SchemaTheme)
										   .map((key: string) => SchemaTheme[key])
										   .filter((val) => !Number.isInteger(val))
										   .map((value: string) => {
											   return {
												   value: value,
												   name: value
											   };
										   });

	selectedRowColoring: GuiSelectOption;

	selectedTheme: GuiSelectOption;

	verticalGrid: boolean;

	horizontalGrid: boolean;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elRef: ElementRef,
				private readonly schemaReadModelRootId: SchemaReadModelRootId,
				private readonly schemaCommandService: SchemaCommandInvoker,
				private readonly schemaReadModelService: SchemaWarehouse) {
		super(changeDetectorRef, elRef);
	}

	ngOnInit() {

		this.schemaReadModelService
			.onRowColoring(this.schemaReadModelRootId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowColoring: SchemaRowColoring) => {
				this.selectedRowColoring = {
					value: SchemaRowColoring[rowColoring],
					name: SchemaRowColoring[rowColoring]
				};
				this.changeDetectorRef.detectChanges();
			});

		this.schemaReadModelService
			.onTheme(this.schemaReadModelRootId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((schemaTheme: SchemaTheme) => {
				this.selectedTheme = {
					value: SchemaTheme[schemaTheme],
					name: SchemaTheme[schemaTheme]
				};
				this.changeDetectorRef.detectChanges();
			});

		this.schemaReadModelService
			.onVerticalGrid(this.schemaReadModelRootId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((verticalGrid: boolean) => {
				this.verticalGrid = verticalGrid;
				this.changeDetectorRef.detectChanges();
			});

		this.schemaReadModelService
			.onHorizontalGrid(this.schemaReadModelRootId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((horizontalGrid: boolean) => {
				this.horizontalGrid = horizontalGrid;
				this.changeDetectorRef.detectChanges();
			});
	}

	toggleTheme(theme: GuiSelectOption): void {
		this.schemaCommandService.setTheme(this.toTheme(theme.value), this.schemaReadModelRootId);
	}

	toggleRowColoring(schemaRowColoring: GuiSelectOption): void {
		this.schemaCommandService.setRowColoring(this.toRowColoring(schemaRowColoring.value), this.schemaReadModelRootId);
	}

	toggleVerticalGrid(verticalGrid: boolean): void {
		this.schemaCommandService.setVerticalGrid(verticalGrid, this.schemaReadModelRootId);
	}

	toggleHorizontalGrid(horizontalGrid: boolean): void {
		this.schemaCommandService.setHorizontalGrid(horizontalGrid, this.schemaReadModelRootId);
	}

	private toTheme(theme: string): SchemaTheme {
		switch (theme.toLowerCase()) {

			case 'fabric':
				return SchemaTheme.FABRIC;

			case 'material':
				return SchemaTheme.MATERIAL;

			case 'generic':
				return SchemaTheme.GENERIC;

			case 'light':
				return SchemaTheme.LIGHT;

			case 'dark':
				return SchemaTheme.DARK;
		}
	}

	private toRowColoring(coloring: string): RowColoring {
		switch (coloring.toLowerCase()) {

			case 'none':
				return RowColoring.NONE;

			case 'odd':
				return RowColoring.ODD;

			case 'even':
				return RowColoring.EVEN;
		}
	}

	protected getSelectorName(): string {
		return 'gui-structure-schema-manager';
	}
}
