import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { distinctUntilChanged, map, take } from 'rxjs/operators';

import { StructureReadModelRepository } from '../../../grid/core/api/read/structure.read-model-repository';
import { StructureSourceOriginRepository } from './origin/structure.source-origin.repository';
import { StructureId } from '../../../core/api/structure.id';
import { structureGlobalId } from '../../../core/api/structure.global-id';
import { StructureReadModelRoot } from '../../../grid/core/api/read/structure.read-model-root';
import { ItemEntity } from '../domain/core/item/item.entity';
import { StructurePreparedItemsRepository } from './prepared/structure.prepared-items.repository';
import { OriginItemEntity } from '../domain/origin/origin-item-entity';
import { Warehouse } from '@generic-ui/hermes';


@Injectable()
export class SourceWarehouse implements Warehouse {

	constructor(private structureRepository: StructureReadModelRepository,
				private structurePreparedItemsRepository: StructurePreparedItemsRepository,
				private structureSourceOriginRepository: StructureSourceOriginRepository) {
	}

	getEntities(structureId: StructureId = structureGlobalId): Array<ItemEntity> {
		return this.structureRepository.getStructure(structureId).getEntities();
	}

	onEntities(structureId: StructureId = structureGlobalId): Observable<Array<ItemEntity>> {

		return this.structureRepository
				   .onStructure(structureId)
				   .pipe(
					   // tap(() => {
					   //    console.log('onEntities');
					   // }),
					   map((structure: StructureReadModelRoot) => structure.getEntities()),
					   distinctUntilChanged((ents1: Array<ItemEntity>, ents2: Array<ItemEntity>) => {
						   if (ents1.length !== ents2.length) {
							   return false;
						   }

						   let flag = true;

						   ents1.forEach((ent, index) => {
							   if (!ent.equals(ents2[index])) {
								   flag = false;
								   return;
							   }
						   });

						   return flag;
					   })
				   );
	}

	onEntitiesSize(structureId: StructureId = structureGlobalId): Observable<number> {

		return this.onEntities(structureId)
				   .pipe(
					   map((entities: Array<ItemEntity>) => {
						   return entities.length;
					   })
				   );
	}

	onSingleEntities(structureId: StructureId = structureGlobalId): Observable<Array<ItemEntity>> {

		return this.onEntities(structureId)
				   .pipe(
					   take(1)
				   );
	}

	onOriginSize(structureId: StructureId = structureGlobalId): Observable<number> {
		return this.structureSourceOriginRepository
				   .onOrigin(structureId)
				   .pipe(
					   map(origin => origin.length)
				   );
	}

	onLoading(structureId: StructureId = structureGlobalId): Observable<boolean> {
		return this.structureRepository
				   .onStructure(structureId)
				   .pipe(
					   map((structure: StructureReadModelRoot) => structure.getSource().isLoading())
				   );
	}

	onPreparedEntities(structureId: StructureId = structureGlobalId): Observable<Array<OriginItemEntity>> {
		return this.structurePreparedItemsRepository.onPreparedItems(structureId);
	}

	getPreparedEntities(structureId: StructureId = structureGlobalId): Array<OriginItemEntity> {
		return this.structurePreparedItemsRepository.getPreparedItems(structureId);
	}

}
