import { Directive, Input, OnChanges } from '@angular/core';
import { Gate } from '../../../../../../common/cdk/component/gate';
import { GuiRowClass } from '../../../../../../gui/grid/core/api/gui.grid.public-api';
import { SchemaRowClass } from '../../../../../../schema/core/api/styling/schema.row-class';
import { SchemaRowClassArchive } from '../../../../../../schema/core/api/styling/schema.row-class.archive';
import { NgChanges } from '../../../../../../common/cdk/component/ng-changes';

@Directive({
	selector: 'gui-structure[rowClass]'
})
export class StructureRowClassGate extends Gate implements OnChanges {

	@Input()
	rowClass: GuiRowClass;

	constructor(private readonly schemaRowClassArchive: SchemaRowClassArchive) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureRowClassGate>) {

		if (this.isDefined('rowClass', changes)) {
			this.schemaRowClassArchive.next(this.rowClass as SchemaRowClass);
		}
	}

}
