import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { AddFilterCommand } from './add-filter.command';
import { StructureAggregate } from '../../../../../core/domain/structure.aggregate';
import { FieldId } from '../../../../../field/core/domain/field/field.id';
import { FilterTypeId } from '../../type/filter-type.id';


@Injectable()
export class AddFilterCommandHandler implements CommandHandler<StructureAggregate, AddFilterCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<AddFilterCommand> {
		return AddFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: AddFilterCommand): void {
		this.domainEventPublisher.publishFromAggregate(aggregate);
	}

	handleAggregate(structure: StructureAggregate, command: AddFilterCommand): void {

		const fieldId: FieldId = command.getFieldId(),
			filterTypeId: FilterTypeId = command.getFilterTypeId(),
			value: any = command.getValue();

		structure.addFilter(fieldId, filterTypeId, value);
	}

}
