import { Injectable } from '@angular/core';

import { Override } from '../../../../../common/cdk/decorators';

import { SourceCommandInvoker } from '../../../../source/core/api/source.command-invoker';
import { StructureId } from '../../../../core/api/structure.id';
import { SourceDispatcher } from '../../../../source/core/domain/source.dispatcher';
import { SourceWarehouse } from '../../../../source/core/api/source.warehouse';
import { StructureEditSourceItemParams } from '../../../../source/core/domain/origin/edit/structure.edit-source-item.params';
import { FieldWarehouse } from '../../../../field/core/api/field.warehouse';
import { CommandDispatcher } from '@generic-ui/hermes';


@Injectable()
export class LocalSourceCommandDispatcher extends SourceCommandInvoker {

	constructor(private readonly structureId: StructureId,
				commandDispatcher: CommandDispatcher,
				sourceDispatcher: SourceDispatcher,
				fieldWarehouse: FieldWarehouse,
				sourceReadModelService: SourceWarehouse) {
		super(commandDispatcher, sourceDispatcher, fieldWarehouse, sourceReadModelService);
	}

	@Override
	setOrigin(items: Array<any>): void {
		super.setOrigin(items, this.structureId);
	}

	@Override
	setLoading(enabled: boolean): void {
		super.setLoading(enabled, this.structureId);
	}

	@Override
	editItem(params: StructureEditSourceItemParams): void {
		super.editItem(params, this.structureId);
	}

	@Override
	editItemByIndex(itemIndex: number, fieldIndex: number, value: any): void {
		super.editItemByIndex(itemIndex, fieldIndex, value, this.structureId);
	}

}
