import { ChangeDetectionStrategy, Component, ElementRef, Input, OnChanges, ViewEncapsulation } from '@angular/core';
import { PureComponent } from '../../../../../common/cdk/component/pure-component';
import { NgChanges } from '../../../../../common/cdk/component/ng-changes';

@Component({
	selector: 'gui-bar-view[value]',
	template: `
		<div class="gui-percentage-bar">
			<div class="gui-percentage" [style.width.%]="width">
			</div>
			<gui-percentage-view *ngIf="showPercentage"
								 [value]="value">
			</gui-percentage-view>
		</div>
	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class BarViewComponent extends PureComponent implements OnChanges {

	@Input()
	value: number;

	@Input()
	showPercentage: boolean = false;

	width: number;

	constructor(elementRef: ElementRef) {
		super(elementRef);
	}

	ngOnChanges(changes: NgChanges<BarViewComponent>) {
		if (changes.value !== undefined && changes.value !== null) {
			this.width = this.value > 100 ? 100 : this.value;
		}
	}

	protected getSelectorName(): string {
		return 'gui-bar-view';
	}

}
