import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { RemoveFilterCommand } from './remove-filter.command';
import { StructureAggregate } from '../../../../../core/domain/structure.aggregate';


@Injectable()
export class RemoveFilterCommandHandler implements CommandHandler<StructureAggregate, RemoveFilterCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<RemoveFilterCommand> {
		return RemoveFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: RemoveFilterCommand): void {
		this.domainEventPublisher.publishFromAggregate(aggregate);
	}

	handleAggregate(structure: StructureAggregate, command: RemoveFilterCommand): void {

		const filterId = command.getFilterId();

		structure.removeFilter(filterId);
	}

}
