import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnChanges, Output, ViewEncapsulation } from '@angular/core';

import { Paging } from '../../../core/api/read/paging';
import { PagingCommandInvoker } from '../../../core/api/paging.command-invoker';
import { PureComponent } from '../../../../../common/cdk/component/pure-component';


@Component({
	selector: 'div[gui-paging-alternative-navigator][paging][sourceSize]',
	templateUrl: `./alternative-paging-navigator.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class AlternativePagingNavigatorComponent extends PureComponent implements OnChanges {

	@Input()
	paging: Paging;

	@Input()
	sourceSize: number;

	@Output()
	nextPageChanged = new EventEmitter();

	@Output()
	prevPageChanged = new EventEmitter();

	prevDisabled: boolean = false;

	nextDisabled: boolean = false;

	constructor(elRef: ElementRef,
				private readonly pagingCommandService: PagingCommandInvoker) {
		super(elRef);
	}

	ngOnChanges() {
		this.calculatePrev();
		this.calculateNext();
	}

	prevPage(): void {
		this.prevPageChanged.emit();
	}

	nextPage(): void {
		this.nextPageChanged.emit();
	}

	firstPage(): void {
		this.pagingCommandService.goToPage(1, this.paging.getPage());
	}

	lastPage(): void {
		let numberOfPages = Math.ceil(this.sourceSize / this.paging.getPageSize());
		this.pagingCommandService.goToPage(numberOfPages, this.paging.getPage());
	}

	private calculatePrev(): void {

		if (!this.paging) {
			return;
		}

		this.prevDisabled = this.paging.isPrevPageDisabled();
	}

	private calculateNext(): void {

		if (!this.paging && !this.sourceSize) {
			return;
		}

		this.nextDisabled = this.paging.isNextPageDisabled();
	}

	protected getSelectorName(): string {
		return 'gui-paging-alternative-navigator';
	}

}
