import { KeyMap, Optional } from '@generic-ui/hermes';

import { FieldId } from '../../../../field/core/domain/field/field.id';
import { OriginItemEntity } from '../../../../source/core/domain/origin/origin-item-entity';
import { Field } from '../../../../field/core/domain/field/field';
import { UniqueValueCollection } from './unique-value-collection';
import { UniqueValue } from './unique-value';
import { UniqueValueId } from './unique-value-id';


export class UniqueFilterManager {

	private readonly uniqueValueMap = new KeyMap<FieldId, UniqueValueCollection>();

	calculate(entities: Array<OriginItemEntity>, field: Field): void {

		const fieldId = field.getId();

		const optUVM = this.uniqueValueMap.get(fieldId);

		optUVM.ifEmpty(() => {

			const set = new Set();

			for (let entity of entities) {
				set.add(field.getValue(entity));
			}

			// sort
			const sortedValues = Array.from(set.values()).sort((a: any, b: any) => {
				return (field as any).getField().sort(a, b);
			});

			const uvc = new UniqueValueCollection(sortedValues, field);

			this.uniqueValueMap.set(field.getId(), uvc);
		});

	}

	calculateAll(entities: Array<OriginItemEntity>, fields: Array<Field>): void {
		for (let field of fields) {
			this.calculate(entities, field);
		}
	}

	filterAll(entities: Array<OriginItemEntity>, fields: Array<Field>): Array<OriginItemEntity> {

		let values: Array<OriginItemEntity> = entities;

		for (let field of fields) {

			values = this.filter(values, field);
		}

		return values;
	}

	filter(entities: Array<OriginItemEntity>, field: Field): Array<OriginItemEntity> {

		let values: Array<OriginItemEntity> = [];

		this.uniqueValueMap
			.get(field.getId())
			.ifPresent((uvc: UniqueValueCollection) => {

				if (uvc.isAllSelected()) {
					values = entities;
				} else if (uvc.isAllDisabled()) {
					values = [];
				} else {

					values = entities.filter((itemEntity: OriginItemEntity) => {

						for (let notSelectedValue of uvc.getNotSelected()) {

							if ((field.getField() as any).equals(itemEntity, (notSelectedValue as any).getValue())) {
								return false;
							}
						}

						return true;
					});
				}

			});

		return values;
	}

	selectAll(fieldId: FieldId): void {

		this.uniqueValueMap
			.get(fieldId)
			.ifPresent((uvc: UniqueValueCollection) => {
				uvc.selectAll();
			});
	}

	select(fieldId: FieldId, uniqueValueId: UniqueValueId): void {

		this.uniqueValueMap
			.get(fieldId)
			.ifPresent((uvc: UniqueValueCollection) => {
				uvc.select(uniqueValueId);
			});
	}

	unselectAll(fieldId: FieldId): void {

		this.uniqueValueMap
			.get(fieldId)
			.ifPresent((uvc: UniqueValueCollection) => {
				uvc.unselectAll();
			});
	}

	unselect(fieldId: FieldId, uniqueValueId: UniqueValueId): void {

		this.uniqueValueMap
			.get(fieldId)
			.ifPresent((uvc: UniqueValueCollection) => {
				uvc.unselect(uniqueValueId);
			});
	}

	getAll(fields: Array<Field>): Map<string, Array<UniqueValue>> {

		let map = new Map();

		for (let field of fields) {

			this.getValues(field)
				.ifPresent((v) => {
					map.set(field.getId().toString(), v);
				});
		}

		return map;
	}

	getValues(field: Field): Optional<Array<UniqueValue>> {

		return this.uniqueValueMap
				   .get(field.getId())
				   .map((uvc: UniqueValueCollection) => {
					   return uvc.getAll();
				   });
	}

}
