import { Key } from '@generic-ui/hermes';

export class FieldId implements Key {

	private readonly id: string;

	constructor(id: string) {
		this.id = id;
	}

	getId(): string {
		return this.id;
	}

	equals(fieldId: FieldId): boolean {
		return fieldId.toString() === this.id;
	}

	toString(): string {
		return this.id;
	}

}
