import { ModifierApi } from './modifier-api';

export abstract class Modifier<T extends ModifierApi> {

	protected constructor(
		private readonly hostElement?: HTMLElement
	) {
	}

	protected abstract createModifier(htmlElement: HTMLElement): T;

	getElement(htmlElement: HTMLElement): T {
		return this.createModifier(htmlElement);
	}

	getHost(): T {

		if (!this.hostElement) {
			throw new Error('Missing host element in DomRenderer constructor.');
		}

		return this.createModifier(this.hostElement);
	}
}
