import { ChangeDetectorRef, ElementRef } from '@angular/core';

import { StreamCloser } from '../reactive/stream-closer';
import { GuiComponent } from './gui-component';
import { Subscription } from 'rxjs';

export abstract class SmartComponent extends GuiComponent {

	private viewInDom: boolean = false;

	private readonly streamCloser = new StreamCloser();

	protected constructor(protected readonly detector: ChangeDetectorRef,
						  elementRef: ElementRef) {
		super(elementRef);
	}

	ngAfterViewInit() {
		this.viewInDom = true;
	}

	ngOnDestroy() {
		this.streamCloser.unsubscribe();
	}

	reRender(): void {
		if (this.isViewInDom()) {
			this.detector.detectChanges();
		}
	}

	isViewInDom(): boolean {
		return this.viewInDom;
	}

	subscribeAndRender(obs$: any, callback: (args: any) => void): Subscription {
		return obs$
			.pipe(
				this.takeUntil()
			)
			.subscribe((subscribeArguments: any) => {
				callback(subscribeArguments);
				this.reRender();
			});
	}

	subscribeWithoutRender(obs$: any, callback: (args: any) => void): Subscription {
		return obs$
			.pipe(
				this.takeUntil()
			)
			.subscribe((subscribeArguments: any) => {
				callback(subscribeArguments);
			});
	}

	protected unsubscribe(): void {
		this.streamCloser.unsubscribe();
	}

	protected takeUntil() {
		return this.streamCloser.takeUntil();
	}

}
