import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { SelectAllUniqueFilterCommand } from './select-all-unique-filter.command';
import { StructureAggregate } from '../../../../../core/domain/structure.aggregate';
import { FieldId } from '../../../../../field/core/domain/field/field.id';


@Injectable()
export class SelectAllUniqueFilterCommandHandler implements CommandHandler<StructureAggregate, SelectAllUniqueFilterCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<SelectAllUniqueFilterCommand> {
		return SelectAllUniqueFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SelectAllUniqueFilterCommand): void {
		this.domainEventPublisher.publishFromAggregate(aggregate);
	}

	handleAggregate(structure: StructureAggregate, command: SelectAllUniqueFilterCommand): void {

		const fieldId: FieldId = command.getFieldId();

		structure.selectAllUniqueFilter(fieldId);
	}

}
