import { Directive, Input, OnChanges } from '@angular/core';

import { Gate } from '../../../../../common/cdk/component/gate';
import { SummariesConfig } from '../../../../summaries/core/api/summaries-config';
import { StructureSummariesConfigService } from '../../panel/summaries/structure.summaries-config.service';
import { NgChanges } from '../../../../../common/cdk/component/ng-changes';

@Directive({
	selector: 'gui-structure[summaries]'
})
export class StructureSummariesGate extends Gate implements OnChanges {

	@Input()
	summaries: SummariesConfig;

	constructor(private readonly structureSummariesConfigService: StructureSummariesConfigService) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureSummariesGate>) {

		if (this.isDefined('summaries', changes)) {
			this.structureSummariesConfigService.set(this.summaries);
		}

	}

}
