import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SchemaRowColoringRepository } from './schema.row-coloring.repository';
import { SchemaId } from '../../api/schema.id';
import { RowColoringSetEvent } from '../../domain/row-coloring/row-coloring-set.event';


@Injectable()
export class SchemaRowColoringSetEventHandler implements DomainEventHandler<SchemaId, RowColoringSetEvent> {

	constructor(private schemaRowColoringRepository: SchemaRowColoringRepository) {
	}

	forEvent(): DomainEventType<RowColoringSetEvent> {
		return RowColoringSetEvent;
	}

	handle(event: RowColoringSetEvent): void {

		if (event.ofMessageType('RowColoringSetEvent')) {
			this.schemaRowColoringRepository.next(event.getAggregateId(), event.getRowColoring());
		}

	}

}
