import { Injectable } from '@angular/core';

import { CommandDispatcher, CommandInvoker, DomainEventBus } from '@generic-ui/hermes';

import { StructureId } from '../../../core/api/structure.id';
import { structureGlobalId } from '../../../core/api/structure.global-id';
import { InitFieldsCommand } from '../domain/init/init-fields.command';
import { FieldConfig } from './field.config';


@Injectable()
export class FieldCommandInvoker implements CommandInvoker {

	constructor(private readonly domainEventBus: DomainEventBus,
				private readonly commandDispatcher: CommandDispatcher) {
	}

	initFields(fieldConfigs: Array<FieldConfig>, structureId: StructureId = structureGlobalId): void {
		this.commandDispatcher.dispatch(new InitFieldsCommand(structureId, fieldConfigs));
	}

}
