import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { Override } from '../../../../../common/cdk/decorators';

import { SourceWarehouse } from '../../../../source/core/api/source.warehouse';
import { StructureId } from '../../../../core/api/structure.id';
import { StructureReadModelRepository } from '../../../core/api/read/structure.read-model-repository';
import { StructureSourceOriginRepository } from '../../../../source/core/api/origin/structure.source-origin.repository';
import { ItemEntity } from '../../../../source/core/domain/core/item/item.entity';
import { StructurePreparedItemsRepository } from '../../../../source/core/api/prepared/structure.prepared-items.repository';
import { OriginItemEntity } from '../../../../source/core/domain/origin/origin-item-entity';


@Injectable()
export class LocalSourceWarehouse extends SourceWarehouse {

	constructor(private structureId: StructureId,
				structureRepository: StructureReadModelRepository,
				structurePreparedItemsRepository: StructurePreparedItemsRepository,
				structureSourceOriginRepository: StructureSourceOriginRepository) {
		super(structureRepository, structurePreparedItemsRepository, structureSourceOriginRepository);
	}

	@Override
	getEntities(): Array<ItemEntity> {
		return super.getEntities(this.structureId);
	}

	@Override
	onEntities(): Observable<Array<ItemEntity>> {
		return super.onEntities(this.structureId);
	}

	@Override
	onEntitiesSize(): Observable<number> {
		return super.onEntitiesSize(this.structureId);
	}

	@Override
	onSingleEntities(): Observable<Array<ItemEntity>> {
		return super.onSingleEntities(this.structureId);
	}

	@Override
	onOriginSize(): Observable<number> {
		return super.onOriginSize(this.structureId);
	}

	@Override
	onLoading(): Observable<boolean> {
		return super.onLoading(this.structureId);
	}

	@Override
	onPreparedEntities(): Observable<Array<OriginItemEntity>> {
		return super.onPreparedEntities(this.structureId);
	}

}
