import { Injectable } from '@angular/core';

import { CommandHandler, CommandType } from '@generic-ui/hermes';
import { SourceDomainEventPublisher } from '../../source.domain-event.publisher';
import { StructureAggregate } from '../../../../../core/domain/structure.aggregate';

import { SetOriginCommand } from './set-origin.command';


@Injectable()
export class SetOriginCommandHandler implements CommandHandler<StructureAggregate, SetOriginCommand> {

	constructor(private structureSourceDomainEventPublisher: SourceDomainEventPublisher) {
	}

	forCommand(): CommandType<SetOriginCommand> {
		return SetOriginCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetOriginCommand): void {

		const aggregateEvents = aggregate.getEvents();

		this.structureSourceDomainEventPublisher.publish(aggregateEvents);
	}

	handleAggregate(structure: StructureAggregate, command: SetOriginCommand): void {

		const items = command.getItems();

		structure.setOrigin(items);
	}

}
