import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SchemaVerticalGridRepository } from './schema.vertical-grid.repository';
import { SchemaId } from '../../api/schema.id';
import { SchemaVerticalGridSetEvent } from '../../domain/grid/vertical/schema-vertical-grid-set.event';


@Injectable()
export class SchemaVerticalGridSetEventHandler implements DomainEventHandler<SchemaId, SchemaVerticalGridSetEvent> {

	constructor(private schemaVerticalGridRepository: SchemaVerticalGridRepository) {
	}

	forEvent(): DomainEventType<SchemaVerticalGridSetEvent> {
		return SchemaVerticalGridSetEvent;
	}

	handle(event: SchemaVerticalGridSetEvent): void {

		if (event.ofMessageType('SchemaVerticalGridSetEvent')) {
			this.schemaVerticalGridRepository.next(event.getAggregateId(), event.getVerticalGrid());
		}

	}

}
