import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { SetConfigQuickFilterCommand } from './set-config-quick-filter.command';
import { ConfigQuickFilterSetEvent } from './config-quick-filter-set.event';

import { StructureAggregateRepository } from '../../../../core/domain/structure-aggregate.repository';
import { StructureAggregate } from '../../../../core/domain/structure.aggregate';


@Injectable()
export class SetConfigQuickFilterCommandHandler implements CommandHandler<StructureAggregate, SetConfigQuickFilterCommand> {

	constructor(private readonly structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<SetConfigQuickFilterCommand> {
		return SetConfigQuickFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetConfigQuickFilterCommand): void {

		const structureId = command.getAggregateId(),
			quickFiltersConfig = command.getConfig();

		this.domainEventPublisher.publish(new ConfigQuickFilterSetEvent(structureId, quickFiltersConfig.enabled));
	}

	handleAggregate(aggregate: StructureAggregate, command: SetConfigQuickFilterCommand): void {

		const quickFiltersConfig = command.getConfig();

		aggregate.setQuickFiltersConfig(quickFiltersConfig);
	}

}
