import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnChanges, Output, ViewEncapsulation } from '@angular/core';

import { Paging } from '../../core/api/read/paging';
import { GuiSelectOption } from '@generic-ui/fabric';
import { PureComponent } from '../../../../common/cdk/component/pure-component';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';


@Component({
	selector: 'div[gui-paging-select][paging]',
	templateUrl: `./paging-select.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class PagingSelectComponent extends PureComponent implements OnChanges {

	@Input()
	paging: Paging;

	@Output()
	pageSizeChanged = new EventEmitter();

	selectPageSizes: Array<GuiSelectOption>;

	selectPageSize: GuiSelectOption;

	constructor(elementRef: ElementRef) {
		super(elementRef);
	}

	ngOnChanges(changes: NgChanges<PagingSelectComponent>) {

		if (changes.paging) {
			if (this.paging) {
				this.selectPageSizes = this.getSelectPageSizes();
				this.selectPageSize = this.getSelectPageSize(this.paging.getPageSize());
			}
		}
	}

	changePageSize(pageSize: GuiSelectOption): void {
		this.pageSizeChanged.emit(+pageSize.value);
	}

	private getSelectPageSizes(): Array<GuiSelectOption> {
		const selectPageSizes: Array<GuiSelectOption> = [],
			pageSizes = this.paging.getPageSizes();

		pageSizes.forEach((pageSize: number) => {
			const selectOption = this.getSelectPageSize(pageSize);
			selectPageSizes.push(selectOption);
		});

		return selectPageSizes;
	}

	private getSelectPageSize(pageSize: number): GuiSelectOption {
		return { name: pageSize.toString(), value: pageSize.toString() };
	}

	protected getSelectorName(): string {
		return 'gui-paging-select';
	}

}
