import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';

import { Warehouse } from '@generic-ui/hermes';

import { schemaGlobalId } from '../domain-read/schema.global-id';
import { SchemaTheme } from './schema-theme';
import { SchemaReadModelRootId } from '../domain-read/schema.read-model-root-id';
import { SchemaCssClass } from './css-classes/schema-css-class';
import { SchemaThemeRepository } from '../domain-read/theme/schema.theme.repository';
import { SchemaCssClassesRepository } from '../domain-read/css-classes/schema.css-classes.repository';
import { SchemaRowColoring } from './schema-row-coloring';
import { SchemaHorizontalGridRepository } from '../domain-read/horizontal-grid/schema.horizontal-grid.repository';
import { SchemaRowColoringRepository } from '../domain-read/row-coloring/schema.row-coloring.repository';
import { SchemaVerticalGridRepository } from '../domain-read/vertical-grid/schema.vertical-grid.repository';


@Injectable()
export class SchemaWarehouse implements Warehouse {

	constructor(private readonly schemaCssClassesRepository: SchemaCssClassesRepository,
				private readonly schemaThemeRepository: SchemaThemeRepository,
				private readonly schemaHorizontalGridRepository: SchemaHorizontalGridRepository,
				private readonly schemaRowColoringRepository: SchemaRowColoringRepository,
				private readonly schemaVerticalGridRepository: SchemaVerticalGridRepository) {
	}

	onTheme(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaTheme> {
		return this.schemaThemeRepository
				   .on(schemaId.toAggregateId());
	}

	onHorizontalGrid(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<boolean> {
		return this.schemaHorizontalGridRepository.on(schemaId.toAggregateId());
	}

	onVerticalGrid(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<boolean> {
		return this.schemaVerticalGridRepository.on(schemaId.toAggregateId());
	}

	onRowColoring(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaRowColoring> {
		return this.schemaRowColoringRepository.on(schemaId.toAggregateId());
	}

	onSingleTheme(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaTheme> {
		return this.schemaThemeRepository
				   .on(schemaId.toAggregateId())
				   .pipe(
					   take(1)
				   );
	}

	onCssClasses(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaCssClass> {
		return this.schemaCssClassesRepository
				   .on(schemaId.toAggregateId());
	}

}
