import { Injectable } from '@angular/core';


import { CommandDispatcher } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/api/structure.id';
import { ToggleSelectedRowCommand } from './toggle/toggle-selected-row.command';
import { RowSelectToggleType } from './core/row-select-toggle-type';

@Injectable()
export class FormationDispatcher {

	constructor(private commandDispatcher: CommandDispatcher) {
	}

	toggleSelectedRow(structureId: StructureId, selectedRow: string, type: RowSelectToggleType): void {
		this.commandDispatcher.dispatch(new ToggleSelectedRowCommand(structureId, selectedRow, type));
	}

}
