import { StructureId } from '../../../../../core/api/structure.id';
import { FieldId } from '../../../../../field/core/domain/field/field.id';
import { FilterTypeId } from '../../type/filter-type.id';
import { StructureCommand } from '../../../../../core/domain/structure.command';


export class AddFilterCommand extends StructureCommand {

	constructor(structureId: StructureId,
				private readonly fieldId: FieldId,
				private readonly filterTypeId: FilterTypeId,
				private readonly value: any) {
		super(structureId, 'AddFilterCommand');
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getFilterTypeId(): FilterTypeId {
		return this.filterTypeId;
	}

	getValue(): any {
		return this.value;
	}

}
