import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';

import { SelectUniqueFilterCommand } from './select-unique-filter.command';
import { StructureAggregate } from '../../../../../core/domain/structure.aggregate';
import { FieldId } from '../../../../../field/core/domain/field/field.id';


@Injectable()
export class SelectUniqueFilterCommandHandler implements CommandHandler<StructureAggregate, SelectUniqueFilterCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<SelectUniqueFilterCommand> {
		return SelectUniqueFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SelectUniqueFilterCommand): void {
		this.domainEventPublisher.publishFromAggregate(aggregate);
	}

	handleAggregate(structure: StructureAggregate, command: SelectUniqueFilterCommand): void {

		const fieldId: FieldId = command.getFieldId(),
			uniqueValueId = command.getUniqueValueId();

		structure.selectUniqueFilter(fieldId, uniqueValueId);
	}

}
