import { StructureId } from '../../../../../core/api/structure.id';
import { FieldId } from '../../../../../field/core/domain/field/field.id';
import { UniqueValueId } from '../unique-value-id';
import { StructureCommand } from '../../../../../core/domain/structure.command';


export class UnselectUniqueFilterCommand extends StructureCommand {

	constructor(structureId: StructureId,
				private readonly fieldId: FieldId,
				private readonly uniqueValueId: UniqueValueId) {
		super(structureId, 'UnselectUniqueFilterCommand');
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getUniqueValueId(): UniqueValueId {
		return this.uniqueValueId;
	}

}
