import { filter, take } from 'rxjs/operators';

import { CommandDispatcher, DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { InitFieldsCommand } from '../../../../field/core/domain/init/init-fields.command';
import { FieldConfig } from '../../../../field/core/api/field.config';
import { FieldsInitedEvent } from '../../../../field/core/domain/init/fields-inited.event';
import { StructureId } from '../../../../core/api/structure.id';
import { Field } from '../../../../field/core/domain/field/field';
import { ColumnFieldId } from '../../../../../composition/core/domain/column/field/column-field.id';
import { ColumnConfig, MultiColumnConfig } from '../../../../../composition/core/api/column/column.config';
import { ColumnParams } from '../../../../../composition/core/domain/column/set-columns/column.params';
import { CompositionCommandInvoker } from '../../../../../composition/core/api/composition.command-invoker';
import { CompositionId } from '../../../../../composition/core/api/composition.id';
import { ColumnFieldFactory } from '../../../../../composition/core/domain/column/field/colum-field.factory';
import { NgChanges } from '../../../../../common/cdk/component/ng-changes';
import { StructureGateway } from '../structure.gateway';


export class StructureColumnInputHandler {

	constructor(private readonly structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly commandDispatcher: CommandDispatcher,
				private readonly domainEventBus: DomainEventBus,
				private readonly columnFieldFactory: ColumnFieldFactory,
				private readonly compositionCommandInvoker: CompositionCommandInvoker) {
	}

	handle(changes: NgChanges<StructureGateway>): void {

		const config: Array<ColumnConfig | MultiColumnConfig> = changes.columns.currentValue;

		this.compositionCommandInvoker.setGroups(config, this.compositionId);

		// prepare model
		const { columns, groups } = this.getConfigs(changes.columns.currentValue);

		const fieldConfigs = this.getFieldConfigs(columns);

		// const fieldConfigs = columns as Array<FieldConfig>;

		const initCommand = new InitFieldsCommand(this.structureId, fieldConfigs);

		this.domainEventBus
			.ofEvent(
				FieldsInitedEvent as any
			)
			.pipe(
				filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === this.structureId.toString()),
				take(1)
			)
			.subscribe((event: FieldsInitedEvent) => {

				const fields = event.getFields(),
					columnFieldIds = this.convertColumnFieldIds(fields),
					params = this.convertColumns(columns, fields, columnFieldIds);

				this.compositionCommandInvoker.setColumns(params, this.compositionId);

				// this.compositionCommandInvoker.setGroups(this.compositionId);
			});

		this.commandDispatcher.dispatch(initCommand);
	}

	private getConfigs(configs: Array<ColumnConfig | MultiColumnConfig>): { columns: Array<ColumnConfig>, groups: Array<MultiColumnConfig> } {

		const columnConfigs: Array<ColumnConfig> = [],
			groupConfigs: Array<MultiColumnConfig> = [];

		for (let i = 0; i < configs.length; i += 1) {

			const config = configs[i];

			if ((config as MultiColumnConfig).columns !== undefined) {
				groupConfigs.push(config);
				columnConfigs.push(...(config as MultiColumnConfig).columns);
			} else {
				columnConfigs.push(config);
			}
		}

		return {
			columns: columnConfigs,
			groups: groupConfigs
		};
	}

	private getFieldConfigs(columns: Array<ColumnConfig>): Array<FieldConfig> {

		return columns.map((c) => {
			return {
				field: c.field as string,
				type: c.type,
				matcher: c.matcher,
				summaries: c.summaries,
				sorting: c.sorting
			};
		});
	}

	private convertColumnFieldIds(fields: Array<Field>): Array<ColumnFieldId> {

		if (!fields) {
			return [];
		}

		return fields.map((field: Field) => new ColumnFieldId(field.getId().getId()));
	}

	private convertColumns(columns: Array<ColumnConfig>, fields: Array<Field>, columnFieldIds: Array<ColumnFieldId>): Array<ColumnParams> {

		if (!columns) {
			return [];
		}

		return columns.map((column: ColumnConfig, index: number) => {

			const columnFieldId = columnFieldIds[index],
				field = fields[index];

			const columnField = this.columnFieldFactory.create(
				columnFieldId,
				field.getAccessorMethod(),
				field.getDataType(),
				field.getSearchAccessorMethod()
			);

			return new ColumnParams(column, columnField);
		});

	}

}
