import { Injectable } from '@angular/core';

import { CommandHandler, CommandType, DomainEventPublisher } from '@generic-ui/hermes';
import { SetConfigFilterCommand } from './set-config-filter.command';
import { ConfigFilterSetEvent } from './config-filter-set.event';
import { StructureAggregate } from '../../../../core/domain/structure.aggregate';


@Injectable()
export class SetConfigFilterCommandHandler implements CommandHandler<StructureAggregate, SetConfigFilterCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): CommandType<SetConfigFilterCommand> {
		return SetConfigFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetConfigFilterCommand): void {

		const filterConfig = command.getConfig();

		this.domainEventPublisher.publish(new ConfigFilterSetEvent(command.getAggregateId(), filterConfig.enabled));
	}

	handleAggregate(aggregate: StructureAggregate, command: SetConfigFilterCommand): void {

		const filterConfig = command.getConfig();

		aggregate.setFilterConfig(filterConfig);
	}

}
