import { TranslationChange } from '../../../../l10n/core/api/translation-change';
import { defaultTranslation } from '../../../../l10n/core/api/default-translation';

export type FieldAccessor = (element?: any) => any;
export type ViewTemplateFunction = (cellValue: any, item?: any) => string;

export interface GuiMultiColumn {

	header?: string;

	columns?: Array<GuiColumn>;

}

export interface GuiColumn {

	field?: string | FieldAccessor;

	type?: string | GuiDataType;

	view?: string | GuiCellView | ViewTemplateFunction;

	header?: string;

	width?: string | number;

	enabled?: boolean;

	align?: string | GuiColumnAlign;

	summaries?: GuiColumnSummaries;

	sorting?: GuiColumnSorting;

	cellEditing?: GuiColumnCellEditing;

	formatter?: (item: any, index: number) => any;

	matcher?: (item: any) => any;

}

export enum GuiDataType {
	UNKNOWN,
	NUMBER,
	STRING,
	BOOLEAN,
	DATE,
	CUSTOM
}

export enum GuiCellView {
	TEXT,
	CHIP,
	LINK,
	IMAGE,
	BOLD,
	ITALIC,
	CHECKBOX,
	CUSTOM,
	BAR,
	PERCENTAGE_BAR,
	PERCENTAGE
}

export enum GuiNumberCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP
}

export enum GuiStringCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP
}

export enum GuiBooleanCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP,
	CHECKBOX
}

export enum GuiDateCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP
}

export interface GuiPaging {

	enabled?: boolean;

	page?: number;

	pageSize?: number;

	pageSizes?: Array<number>;

	pagerTop?: boolean;

	pagerBottom?: boolean;

	display?: string | GuiPagingDisplay;

}

export enum GuiTheme {
	FABRIC,
	MATERIAL,
	LIGHT,
	DARK,
	GENERIC
}

export enum GuiRowColoring {
	NONE,
	EVEN,
	ODD
}

export interface GuiRowStyle {

	style?: string;

	styleFunction?: (data: any, index: number) => string;

}

export interface GuiRowClass {

	class?: string;

	classFunction?: (data: any, index: number) => string;

}

export enum GuiSortingOrder {
	NONE,
	ASC,
	DESC
}

export interface GuiSorting {

	enabled?: boolean;

	/**
	 * @experimental
	 */
	multiSorting?: boolean;

}

export interface GuiFiltering {

	enabled?: boolean;

}

export interface GuiQuickFilters {

	enabled?: boolean;

}

export interface GuiSearching {

	enabled?: boolean;

	highlighting?: boolean;

	placeholder?: string;

	phrase?: string;

}

export interface GuiSummaries {

	enabled?: boolean;

	top?: boolean;

	bottom?: boolean;

}

export interface GuiColumnSummaries {

	enabled?: boolean;

	summariesTypes?: Array<any>;

}

export interface GuiColumnSorting {

	enabled?: boolean;

	matcher?: (item: any) => any;

	order?: GuiSortingOrder

}

export interface GuiColumnCellEditing {

	enabled?: boolean;

}

export interface GuiColumnMenu {

	enabled?: boolean;

	sort?: boolean;

	filter?: boolean;

	columnsManager?: boolean;

}

export enum GuiPagingDisplay {
	BASIC,
	ADVANCED
}

export enum GuiColumnAlign {
	RIGHT,
	CENTER,
	LEFT
}

export interface GuiRowDetail {

	enabled?: boolean;

	template?: (item: any, index: number) => string;

}

export interface GuiTitlePanel {

	enabled?: boolean;

	template?: string | ((title: any) => string);

}

export interface GuiFooterPanel {

	enabled?: boolean

	template?: string | ((title: any) => string);

}

export interface GuiInfoPanel {

	enabled?: boolean;

	infoDialog?: boolean;

	columnsManager?: boolean;

	schemaManager?: boolean;

	sourceSize?: boolean

}

export enum GuiRowSelectionType {
	ROW,
	CHECKBOX
}

export enum GuiRowSelectionMode {
	SINGLE,
	MULTIPLE
}

export interface GuiRowSelection {

	enabled?: boolean;

	type?: string | GuiRowSelectionType;

	mode?: string | GuiRowSelectionMode;

}

export interface GuiSelectedRow {

	data?: any;

	index: number;

	itemId: any;

}

export interface GuiLocalization {

	translation?: GuiTranslation;

	translationResolver?: (key: string, value: string) => string;

}

export type GuiTranslation = TranslationChange;

export const GuiDefaultTranslation = defaultTranslation;
