import { FilterTypeId } from '../../domain/type/filter-type.id';

export class FilterTypeReadModel {

	private readonly filterTypeId: FilterTypeId;

	private readonly name: string;

	constructor(id: FilterTypeId,
				name: string) {
		this.filterTypeId = id;
		this.name = name;
	}

	getId(): FilterTypeId {
		return this.filterTypeId;
	}

	getName(): string {
		return this.name;
	}

}
