import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { FilterWarehouse } from '../../core/api/filter.warehouse';
import { ActiveFilterReadModel } from '../../core/api/active/active-filter.read-model';
import { StructureId } from '../../../core/api/structure.id';
import { FilterCommandInvoker } from '../../core/api/filter.command-invoker';


@Component({
	selector: 'div[gui-active-filter-list]',
	templateUrl: './active-filter-list.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class ActiveFilterListComponent extends SmartComponent implements OnInit {

	activeFilters: Array<ActiveFilterReadModel> = [];

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly filterCommandDispatcher: FilterCommandInvoker) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {
		this.filterWarehouse
			.onActiveFilters(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((activeFilters: Array<ActiveFilterReadModel>) => {
				this.activeFilters = activeFilters;
				this.changeDetectorRef.detectChanges();
			});
	}

	removeFilter(filter: ActiveFilterReadModel): void {
		this.filterCommandDispatcher.removeFilter(filter.getFilterId(), this.structureId);
	}

	protected getSelectorName(): string {
		return 'gui-active-filter-list';
	}
}
