import { CompositionId } from '../../../api/composition.id';
import { ColumnId } from '../../../api/column/column.id';
import { CompositionCommand } from '../../composition.command';

export class CompositionSetColumnEnabledCommand extends CompositionCommand {

	constructor(readonly compositionId: CompositionId,
				private readonly columnId: ColumnId,
				private readonly enabled: boolean) {
		super(compositionId, 'CompositionSetColumnEnabledCommand');
	}

	getColumnId(): ColumnId {
		return this.columnId;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

}
