import { DataFieldTypes } from './data/data-field-types';
import { DataType } from './data/data-type';
import { NumberDataField } from './data/type/number-data-field';
import { BooleanDataField } from './data/type/boolean-data-field';
import { StringDataField } from './data/type/string-data-field';
import { DateDataField } from './data/type/date-data-field';
import { FieldId } from './field.id';

import { OriginItemEntity } from '../../../../source/core/domain/origin/origin-item-entity';
import { SummariesType } from '../../../../summaries/core/domain/summaries.type';


export class Field {

	static of(id: FieldId, dataField: DataFieldTypes, name: string): Field {
		return new Field(id, dataField, name);
	}

	private readonly id: FieldId;

	private readonly field: DataFieldTypes;

	private readonly name: string;

	constructor(id: FieldId,
				field: DataFieldTypes,
				name: string) {
		this.id = id;
		this.field = field;
		this.name = name;
	}

	getId(): FieldId {
		return this.id;
	}

	getKey(): string {
		return this.getId().toString();
	}

	getDataType(): DataType {
		return this.field.getDataType();
	}

	getName(): string {
		return this.name;
	}

	/**
	 * @deprecated
	 */
	getField(): DataFieldTypes {
		return this.field;
	}

	/**
	 * Probably should not be used.
	 *
	 * @deprecated
	 */
	getAccessor(): string {
		return this.field.getAccessor();
	}

	/**
	 * Probably should not be used.
	 *
	 * @deprecated
	 */
	getAccessorMethod(): (entity: OriginItemEntity) => any {
		return this.field.getAccessorMethod();
	}

	/**
	 * Probably should not be used.
	 *
	 * @deprecated
	 */
	getSearchAccessorMethod(): (entity: OriginItemEntity) => any {
		return this.field.getSearchAccessorMethod();
	}

	getValue(entity: OriginItemEntity): any {
		return this.field.getValue(entity);
	}

	getDisplayValue(value: any): any {
		return (this.field as any).getDisplayValue(value);
	}

	/**
	 * Probably should not be used.
	 *
	 * @deprecated
	 */
	isSummaries(type: SummariesType): boolean {
		return this.field.isSummaries(type);
	}

	/**
	 * Probably should not be used.
	 *
	 * @deprecated
	 */
	isSummariesEnabled(): boolean {
		return this.field.isSummariesEnabled();
	}

	search(item: OriginItemEntity, searchPhrase: string): boolean {
		return this.field.search(item, searchPhrase);
	}

	filter(item: OriginItemEntity, filterPhrase: any): boolean {

		const value = this.field.getValue(item);

		if (this.field.getDataType() === DataType.NUMBER) {
			return (this.field as NumberDataField).filter(value, filterPhrase);
		} else if (this.field.getDataType() === DataType.BOOLEAN) {
			return (this.field as BooleanDataField).filter(value, filterPhrase);
		} else if (this.field.getDataType() === DataType.STRING) {
			return (this.field as StringDataField).filter(value, filterPhrase);
		} else if (this.field.getDataType() === DataType.DATE) {
			return (this.field as DateDataField).filter(value, filterPhrase);
		} else {
			return true;
		}
	}

	sort(entityOne: OriginItemEntity, entityTwo: OriginItemEntity, direction: boolean): number {

		let valueOne = direction ? this.field.getSortValue(entityOne) : this.field.getSortValue(entityTwo),
			valueTwo = direction ? this.field.getSortValue(entityTwo) : this.field.getSortValue(entityOne);

		if (this.field.getDataType() === DataType.NUMBER) {
			return (this.field as NumberDataField).sort(valueOne, valueTwo);
		} else if (this.field.getDataType() === DataType.BOOLEAN) {
			return (this.field as BooleanDataField).sort(valueOne, valueTwo);
		} else if (this.field.getDataType() === DataType.STRING) {
			return (this.field as StringDataField).sort(valueOne, valueTwo);
		} else if (this.field.getDataType() === DataType.DATE) {
			return (this.field as DateDataField).sort(valueOne, valueTwo);
		} else {
			return 0;
		}
	}

}
