import { UniqueValueId } from '../../domain/unique/unique-value-id';

export class UniqueValueReadModel {

	private readonly id: UniqueValueId;

	private readonly value: any;

	private readonly displayValue: string;

	private readonly enabled: boolean;

	constructor(
		id: UniqueValueId,
		value: any,
		enabled: boolean
	) {
		this.id = id;
		this.value = value;
		this.enabled = enabled;
	}

	getId(): UniqueValueId {
		return this.id;
	}

	getValue(): any {
		return this.value;
	}

	geDisplayValue(): string {
		return this.displayValue;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

}
